


































































































import { Vue, Component } from 'vue-property-decorator';
import BaseChart from '@components/BaseChart/index.vue';
import BusinessLeaderPost from './businessLeaderPost.vue';
import moment from 'moment';
import { currencyFormatter } from '@utils/util';
import {
  queryPointModuleUsage,
  queryDevicesNumber,
  queryVisitDurationDate,
  queryServerSurplus,
  queryDataConcurrency,
  queryUsePeak
} from '@api/moreDimensionMonitor.js';

@Component({
  components: {
    BaseChart,
    BusinessLeaderPost
  }
})
export default class SystemUsage extends Vue {
  private currentTab: any = 1;
  private visitDurationDate: any = [];
  private visitDurationBar: any = {};
  private visitDurationLoading = false;
  private usageSituationTable: any = [];
  private usageSituationColumns: any = [
    { prop: 'moduleName', label: '模块', minWidth: '120' },
    { prop: 'usePersonTime', label: '使用人次', align: 'center' },
    { prop: 'moduleUsageRate', label: '使用率', align: 'center' },
    { prop: 'weekCircleCompare', label: '周环比', align: 'center' }
  ];
  private usageSituationLoading = false;
  private devicesNumberPie: any = {};
  private devicesNumberLoading = false;
  private serverSurplus: any = [];
  private serverSurplusLoading = false;
  private dataConcurrencyLine: any = {};
  private dataConcurrencyLoading = true;
  private usePeakLine: any = {};
  private usePeakLoading = true;

  private mounted() {
    this.visitDurationDate = [
      moment()
        .subtract(7, 'days')
        .format('YYYY-MM-DD'),
      moment().format('YYYY-MM-DD')
    ];
  }
  init() {
    this.getPointModuleUsage();
    this.getDevicesNumber();
    this.getVisitDurationDate();
    this.getServerSurplus();
    this.getDataConcurrency();
    this.getUsePeak();
  }
  //面板切换
  handleSearch() {
    if (this.currentTab === 2) {
      this.init();
    }
  }
  // 访问时长趋势查询
  getVisitDurationDate() {
    this.visitDurationLoading = true;
    const params = {
      endDate: this.visitDurationDate[1],
      startDate: this.visitDurationDate[0]
    };
    queryVisitDurationDate(params).then(
      ({ data, error }: { data: any; error: any }) => {
        if (!error) {
          this.visitDurationLoading = false;
          const xAxis = data.map((item: any) => {
            return item.date;
          });
          const greater = data.map((item: any) => {
            return item.greater;
          });
          const less = data.map((item: any) => {
            return item.less;
          });
          const middle = data.map((item: any) => {
            return item.middle;
          });
          this.visitDurationBar = {
            xAxis: {
              data: xAxis
            },
            series: [
              {
                type: 'bar',
                name: ' >150s',
                data: greater,
                barMaxWidth: 20,
                barMinHeight: 10
              },
              {
                type: 'bar',
                name: ' 90-150s',
                data: middle,
                barMaxWidth: 20,
                barMinHeight: 10
              },
              {
                type: 'bar',
                name: ' <90s',
                data: less,
                barMaxWidth: 20,
                barMinHeight: 10
              }
            ],
            grid: {
              bottom: '6%'
            },
            dataZoom: {
              type: 'slider',
              maxValueSpan: 10,
              height: 10,
              // left: '5%',
              // right: '4%',
              bottom: 0,
              showDetail: false,
              handleIcon:
                'M10.7,11.9H9.3c-4.9,0.3-8.8,4.4-8.8,9.4c0,5,3.9,9.1,8.8,9.4h1.3c4.9-0.3,8.8-4.4,8.8-9.4C19.5,16.3,15.6,12.2,10.7,11.9z M13.3,24.4H6.7V23h6.6V24.4z M13.3,19.6H6.7v-1.4h6.6V19.6z'
            },
            animation: false
          };
        } else {
          this.$message.error('访问时长趋势查询失败：' + error.message);
        }
      }
    );
  }
  // 访问时长趋势时间变化
  handleVisitDurationDate() {
    this.getVisitDurationDate();
  }
  // 重点模块使用情况查询
  getPointModuleUsage() {
    this.usageSituationLoading = true;
    queryPointModuleUsage().then(
      ({ data, error }: { data: any; error: any }) => {
        if (!error) {
          this.usageSituationTable = data;
          this.usageSituationLoading = false;
        } else {
          this.$message.error('重点模块使用情况查询失败：' + error.message);
        }
      }
    );
  }
  // 设备数查询
  getDevicesNumber() {
    this.devicesNumberLoading = true;
    queryDevicesNumber().then(({ data, error }: { data: any; error: any }) => {
      if (!error) {
        this.devicesNumberLoading = false;
        data.forEach((item) => {
          item['name'] = item.equipmentType;
          item['value'] = item.equipmentAmount;
        });
        this.devicesNumberPie = {
          legend: {
            icon: 'circle',
            itemWidth: 10,
            right: '5%',
            top: '2%'
          },
          series: [
            {
              radius: ['50%', '74%'],
              center: ['50%', '60%'],
              avoidLabelOverlap: false,
              label: {
                show: false
              },
              hoverOffset: 6,
              itemStyle: {
                borderWidth: 1,
                borderColor: '#fff'
              },
              data: data
            }
          ],
          color: ['#2F6AFF', '#6FBCFF', '#91D3FF']
        };
      } else {
        this.$message.error('设备数查询失败:' + error.message);
      }
    });
  }
  // 服务器剩余量查询
  getServerSurplus() {
    this.serverSurplusLoading = true;
    queryServerSurplus().then(({ data, error }: { data: any; error: any }) => {
      if (!error) {
        this.serverSurplusLoading = false;
        const rate = parseFloat(data.percent.split('%')[0]) / 100;
        this.serverSurplus = {
          title: {
            text: '剩余容量'
          },
          tooltip: {
            show: false
          },
          series: [
            {
              data: [rate],
              label: {
                normal: {
                  formatter: (params) => {
                    return (params.value * 100).toFixed(2) + '%';
                  }
                }
              }
            }
          ]
        };
      } else {
        this.$message.error('服务器剩余量查询失败：' + error.message);
      }
    });
  }
  // 数据并发量预测
  getDataConcurrency() {
    queryDataConcurrency().then(
      ({ data, error }: { data: any; error: any }) => {
        if (!error) {
          this.dataConcurrencyLoading = false;
          const BASE_TIME = moment(new Date()).format('YYYY-MM-DD');
          const seriesData = data.data.map((item: any) => {
            return [BASE_TIME + ' ' + item.time, item.value];
          });
          this.dataConcurrencyLine = {
            title: {
              text: '目标评估',
              textStyle: {
                color: '#95A4BC',
                fontSize: 14,
                // lineHeight: 22,
                fontFamily: 'PingFangSC-Regular'
              },
              subtext: data.message,
              subtextStyle: {
                color: '#242633',
                fontSize: 20,
                lineHeight: 28,
                fontFamily: 'PingFangSC-Regular'
              },
              left: '-1%'
            },
            xAxis: [
              {
                type: 'time',
                max: `${BASE_TIME} 23:59:59`,
                min: `${BASE_TIME} 00:00:00`,
                splitNumber: 3,
                axisLabel: {
                  color: 'rgba(0,0,0,0.65)',
                  formatter: function(value) {
                    return moment(value)
                      .format('YYYY-MM-DD HH:mm')
                      .substr(11, 20);
                  }
                },
                axisLine: {
                  show: false
                },
                axisTick: {
                  show: false
                },
                splitLine: {
                  show: false
                },
                boundaryGap: ['20%', '20%']
              }
            ],
            yAxis: [
              {
                type: 'value',
                splitNumber: 2,
                max: (value) => {
                  return value.max + 0.05;
                },
                axisLine: {
                  show: false
                },
                axisTick: {
                  show: false
                },
                splitLine: {
                  lineStyle: {
                    color: '#EBECF0',
                    type: 'dashed'
                  }
                },
                axisLabel: {
                  color: 'rgba(0,0,0,0.65)',
                  inside: true,
                  padding: [0, 0, 20, -10],
                  formatter: function(value) {
                    if (value !== 0) {
                      return currencyFormatter(value) + '次';
                    } else {
                      return '';
                    }
                  }
                }
              }
            ],
            grid: {
              left: '5%',
              right: '5%',
              bottom: '2%',
              top: '40%',
              containLabel: true
            },
            series: [
              {
                type: 'line',
                areaStyle: {
                  color: 'rgba(47,106,225,0.35)'
                },
                smooth: true,
                symbol: 'none',
                data: seriesData
              }
            ],
            tooltip: {
              show: false
            },
            color: ['#2F6AFF']
          };
        } else {
          this.$message.error('数据并发量预测查询失败:' + error.message);
        }
      }
    );
  }
  // 使用峰值查询
  getUsePeak() {
    queryUsePeak().then(({ data, error }: { data: any; error: any }) => {
      if (!error) {
        // const day = moment(new Date()).format('YYYY-MM-DD');
        this.usePeakLoading = false;
        if (data.length) {
          data.forEach((item: any) => {
            item.type = 'line';
            item.smooth = true;
            item.symbol = 'none';
            item.data.forEach((typeItem: any) => {
              typeItem.value = [typeItem.time, typeItem.value];
            });
            if (item.name.indexOf('任务') !== -1) {
              item.areaStyle = { color: 'rgba(47,106,225,0.35)' };
            }
          });
          const BASE_TIME = moment(new Date()).format('YYYY-MM-DD');
          this.usePeakLine = {
            xAxis: [
              {
                type: 'time',
                // max: `${BASE_TIME} 23:59:59`,
                // min: `${BASE_TIME} 00:00:00`,
                // splitNumber: 12,
                interval: 60 * 1000 * 30,
                axisLabel: {
                  color: 'rgba(0,0,0,0.65)',
                  margin: 10,
                  formatter: function(value) {
                    return moment(value)
                      .format('YYYY-MM-DD HH:mm')
                      .substr(11, 20);
                  }
                },
                axisLine: {
                  lineStyle: {
                    color: 'rgba(47,106,255,0.20)'
                  }
                },
                axisTick: {
                  color: 'rgba(47,106,255,0.20)'
                },
                splitLine: {
                  show: false
                }
              }
            ],
            yAxis: [
              {
                type: 'value',
                // max: (value) => {
                //     return value.max;
                // },
                // splitNumber: 5,
                axisLine: {
                  show: false
                },
                axisTick: {
                  show: false
                },
                splitLine: {
                  lineStyle: {
                    color: '#EBECF0',
                    type: 'dashed'
                  }
                },
                axisLabel: {
                  color: 'rgba(0,0,0,0.65)',
                  margin: 20
                }
              }
            ],
            grid: {
              left: '0%',
              right: '2%',
              bottom: '8%',
              // top: '20%',
              containLabel: true
            },
            legend: {
              icon: 'circle',
              itemWidth: 10,
              right: '0%',
              top: '2%'
            },
            series: data,
            color: ['#2F6AFF', '#F5A623'],
            dataZoom: [
              {
                type: 'slider',
                height: 18,
                align: 'center',
                left: '3%',
                right: '4%',
                bottom: 0,
                maxValueSpan: 60 * 1000 * 30 * 12,
                startValue: `${BASE_TIME} 7:00`,
                // endValue: `${BASE_TIME} 17:00`,
                labelFormatter: (value) => {
                  return moment(value).format('HH:mm');
                },
                handleIcon:
                  'M10.7,11.9H9.3c-4.9,0.3-8.8,4.4-8.8,9.4c0,5,3.9,9.1,8.8,9.4h1.3c4.9-0.3,8.8-4.4,8.8-9.4C19.5,16.3,15.6,12.2,10.7,11.9z M13.3,24.4H6.7V23h6.6V24.4z M13.3,19.6H6.7v-1.4h6.6V19.6z'
              }
            ]
          };
        }
      } else {
        this.$message.error('使用峰值查询失败：' + error.message);
      }
    });
  }
}
