    <component :is="roleKey"></component>
  </div>
</template>

<script>
// import { mapState } from 'vuex';
import DepartmentChargePage from './authorityManage/moreDimensionMonitor/departmentChargePage.vue';
import DirectorPage from './authorityManage/moreDimensionMonitor/directorPage.vue';
import SystemUsage from './authorityManage/moreDimensionMonitor/systemUsage.vue';
import CurrencyPanel from './authorityManage/moreDimensionMonitor/currencyPanel.vue';
import EmployeePosition from './authorityManage/moreDimensionMonitor/employeePosition.vue';

export default {
  name: 'articles',
  components: {
    DepartmentChargePage,
    DirectorPage,
    SystemUsage,
    CurrencyPanel,
    EmployeePosition
  },
  computed: {
    roleKey() {
      const userStation = this.$store.state.user.userStation;
      if (JSON.stringify(userStation) == '{}') {
        return false;
      }
      let homePage = '';
      if (userStation.some((item) => item.groupNature === '005')) {
        homePage = 'SystemUsage';
      } else if (userStation.some((item) => item.groupNature === '004')) {
        homePage = 'DepartmentChargePage';
      } else if (userStation.some((item) => item.groupNature === '003')) {
        homePage = 'DirectorPage';
      } else if (userStation.some((item) => item.groupNature === '002')) {
        homePage = 'CurrencyPanel';
      } else if (userStation.some((item) => item.groupNature === '001')) {
        homePage = 'EmployeePosition';
      }
      return homePage;
    }
  }
};
</script>

