

















































































































import jquery from 'jquery';
import BpmnViewer from 'bpmn-js/lib/Viewer';
import customModule from './customViewRender';
import { Component, Vue, Prop, Watch } from 'vue-property-decorator';
import previewForm from '../../processConfig/customForm/compoents/previewForm.vue';
import {
  queryEventList,
  queryEventDetail,
  queryHistoryDetail,
  attachmentDownload,
  queryTaskData
} from '@api/processAssignment';
import Base64 from '@utils/Base64';
import cloneDeep from 'lodash/cloneDeep';
import { arrayFind } from '@utils/util';

const enumColor = {
  已办理: 'highlight-overlay-had',
  未办理: 'highlight-overlay-ing',
  chaos: 'highlight-overlay-chaos',
  reject: 'highlight-overlay-reject'
};

@Component({
  components: { previewForm }
})
export default class CirculationDetails extends Vue {
  @Prop() taskId: string;
  @Prop() idName: '';
  @Prop() visible: boolean;
  @Prop() innerDrawer: boolean;
  @Prop({ default: '' }) taskDealStationId: string;
  private innerContent = ''; // deal-办理详情，event-事件列表
  private collapseList: Array<any> = [];
  private eventInfo: any = {};
  private historyDetail: any = {};
  private containerEl: any = null;
  private bpmnModeler: any = null;
  private currentShowTask: any = {};
  private scale = 1;
  private loading = false;
  private customList: any = {};
  private currenClickNodeId: string;
  private currenClickNodeTaskId: string; // 当前点击节点的任务id
  private formAttribute: any = {};
  private nodeAttribute: any = {};
  private taskNodeMap: any = {};
  private tasks: Array<any> = [];
  private templateForms: Array<any> = [];

  taskColumns() {
    return [
      {
        prop: 'station',
        label: '办理岗位',
        'header-align': 'left',
        align: 'left'
      },
      {
        prop: 'handler',
        label: '办理人',
        'header-align': 'left',
        align: 'left'
      },
      {
        prop: 'handleTime',
        label: '办理时间',
        'header-align': 'left',
        align: 'left'
      },
      {
        prop: 'remark',
        label: '备注',
        showOverflowTooltip: true,
        'header-align': 'left',
        align: 'left'
      },
      {
        prop: 'status',
        label: '完成状态',
        'header-align': 'left',
        align: 'left'
      },
      {
        prop: 'operation',
        label: '操作',
        'header-align': 'center',
        align: 'center',
        actions: [
          {
            code: 'edit',
            text: '查看详情',
            onClick: this.getFormDetail
          },
          {
            code: 'log',
            text: '事件日志',
            onClick: this.getEventList
          }
        ]
      }
    ];
  }

  @Watch('visible')
  switchTask(newVal) {
    if (newVal) {
      this.loading = true;
      queryHistoryDetail(this.taskId).then(({ data, error }) => {
        this.loading = false;
        if (!error) {
          this.historyDetail = data;
          // 归集各节点任务
          this.taskNodeMap = {};
          this.historyDetail['nodeInfos'].forEach((item) => {
            if (!this.taskNodeMap[item.taskDefinitionKey]) {
              this.taskNodeMap[item.taskDefinitionKey] = [];
            }
            this.taskNodeMap[item.taskDefinitionKey].push(item);
          });

          this.formAttribute = data['processedForms']
            ? JSON.parse(Base64.decode(data['processedForms']))
            : {};
          this.$nextTick(() => {
            this.drawFlow();
          });
        } else {
          this.$message.error(error.message);
        }
      });
    }
  }

  getTaskHandles() {
    const obj = {
      processInstanceId: this.taskId,
      nodeId: this.currenClickNodeId
    };
    this.tasks = [];
    this.loading = true;
    queryTaskData(obj).then(({ data, error }) => {
      this.loading = false;
      if (!error) {
        this.templateForms = data['templateForms'];
        if (data['taskHandledForms']) {
          // if (data['taskHandledForms'].length === 1) {
          //   this.getFormDetail(data['taskHandledForms'][0]);
          // } else {
          this.tasks = data['taskHandledForms'];
          // }
        }
      } else {
        this.$message.error(error.message);
      }
    });
  }

  handleChange(activeName) {
    if (activeName !== '') {
      this.getEventDetail(activeName);
    }
  }

  getEventDetail(activeName) {
    const obj = {
      code: activeName,
      processInstanceId: this.taskId,
      nodeId: this.currenClickNodeId
    };
    queryEventDetail(obj).then(({ data, error }) => {
      if (!error) {
        this.eventInfo = data;
      } else {
        this.$message.error(error.message);
      }
    });
  }
  getEventList() {
    const obj = {
      processInstanceId: this.taskId,
      nodeId: this.currenClickNodeId
    };
    queryEventList(obj).then(({ data, error }) => {
      if (!error) {
        this.collapseList = [];
        if (data) {
          if (JSON.stringify(data) === '{}') {
            this.$message.info('该节点事件日志为空');
            return false;
          } else {
            for (const key in data) {
              data[key].forEach((item) => {
                this.collapseList.push({
                  code: item.code,
                  name: `${key}-${item.tempName}`
                });
              });
            }
            this.innerDrawer = true;
            this.innerContent = 'event';
          }
        }
      } else {
        this.$message.error(error.message);
      }
    });
  }

  getFormDetail(currentShowTask) {
    if (currentShowTask) {
      this.currentShowTask = currentShowTask;
      const formData = currentShowTask.formData;

      this.innerDrawer = true;
      this.innerContent = 'deal';
      if (this.formAttribute.formList) {
        const formAttribute = cloneDeep(this.formAttribute);
        formAttribute.formList.forEach((item) => {
          item['valueSymbol'] = '';
          item['visibleFrom'] = false;
          item['tableData'] = [];
          // 临时代码，清除历史错误数据
          if (item.showForm) {
            delete item.showForm;
          }
        });
        if (this.templateForms) {
          formAttribute.formList.forEach((item) => {
            const conf = arrayFind(this.templateForms, 'formItem', item.key);
            if (conf) {
              if (conf.editable || conf.readable) {
                item.readable = 1;
              } else {
                item.readable = 0;
                item.editable = 0;
              }
              //  异常情况：data['templateForms']没有，但formList中有，且readable和editable有一个为1
              //  特此处理，将其置为0
            } else {
              item.readable = 0;
              item.editable = 0;
            }
            if (item.type === 'table') {
              if (item.addLine) {
                item.addLine = false;
              }
              if (conf) {
                item.editable = conf.editable;
                item.readable = conf.readable;
                if (conf.extendProp) {
                  const extendPropList = JSON.parse(
                    Base64.decode(conf.extendProp)
                  );
                  if (extendPropList) {
                    const colsList: Array<any> = [];
                    item.cols.forEach((itemCol: any) => {
                      const col = arrayFind(
                        extendPropList,
                        'formItem',
                        itemCol.prop
                      );
                      if (col) {
                        if (col.editable === 1 || col.readable === 1) {
                          colsList.push({
                            ...itemCol,
                            editable: 0,
                            readable: 1
                          });
                        }
                      }
                    });
                    item.cols = colsList;
                  }
                }
              }
            }
            // 回显历史赋值
            if (formData) {
              if (item.type === 'table') {
                item.datas = formData[item.key];
                item.value = formData[item.key];
              } else {
                item.value = formData[item.key];
              }
            }
          });
        }
        this.nodeAttribute = formAttribute;
      }
    } else {
      this.currentShowTask = {};
    }
  }

  drawFlow() {
    if (this.historyDetail.flowChart) {
      this.containerEl = document.getElementById(this.idName);
      this.bpmnModeler && this.bpmnModeler.destroy();
      this.bpmnModeler = new BpmnViewer({
        container: this.containerEl,
        additionalModules: [customModule]
      });
      this.scale = 1;
      const viewer = this.bpmnModeler;
      this.bpmnModeler.importXML(this.historyDetail.flowChart, (err: any) => {
        if (err) {
          console.error(err);
        } else {
          const canvas = viewer.get('canvas');
          canvas.zoom('fit-viewport', 'auto');
          this.setNodeColor();
        }
      });
    } else {
      this.$message.error('流程图为空');
      this.clearFlow();
    }
  }
  setNodeColor() {
    const overlays = this.bpmnModeler.get('overlays');
    Object.keys(this.taskNodeMap).forEach((key) => {
      const elementRegistry = this.bpmnModeler.get('elementRegistry');
      const shape = elementRegistry.get(key);
      if (shape) {
        const tasks = this.taskNodeMap[key];
        /**
         * 如果节点历史不止一个，则关注节点的混合状态
         * 既有已办理，又有未办理，则显示中间色
         */
        let color = '';
        if (tasks.length > 1) {
          if (
            tasks.some((task) => {
              return task['handleResult'] === '未办理';
            })
          ) {
            color = 'chaos';
          }
        } else {
          if (tasks[0]['status'] === '已驳回') {
            color = 'reject';
          } else {
            color = tasks[0]['handleResult'];
          }
        }

        const $overlayHtml = jquery(`<div class=${enumColor[color]}>`).css({
          width: shape.width,
          height: shape.height
        });
        overlays.add(key, {
          position: { top: 0, left: 0 },
          html: $overlayHtml
        });
      }
    });
    this.overlayClick();
  }

  overlayClick() {
    jquery('.view-canvas').on('click', '.djs-overlays', (e) => {
      const parentEle = e.target.parentElement;
      const shapeId = parentEle.getAttribute('data-container-id');
      this.currenClickNodeTaskId = this.taskNodeMap[shapeId][0].taskId;
      this.currenClickNodeId = shapeId;
      this.getTaskHandles();
    });
  }
  clearFlow() {
    const flow = document.getElementById(this.idName);
    if (flow !== null) {
      flow.innerHTML = '';
    }
  }
  download(path) {
    attachmentDownload(path);
  }
  handleZoom(flag) {
    if (flag < 0 && this.scale <= 1) {
      return;
    }
    if (flag > 0 && this.scale >= 3) {
      return;
    }
    this.scale += flag;
    this.$nextTick(() => {
      this.bpmnModeler.get('canvas').zoom('fit-viewport');
    });
  }
  cancelHistroyDialog() {
    this.tasks = [];
    this.$emit('cancel');
  }
}
