import BaseRenderer from 'diagram-js/lib/draw/BaseRenderer'; // 引入默认的renderer
import {
  customElements,
  customConfig
} from '../../../processConfig/processNode/customPalette/custom/util';
import { append as svgAppend, create as svgCreate } from 'tiny-svg';
import { getNodeType } from './utils';

const HIGH_PRIORITY = 1500; // 最高优先级

export default class CustomRenderer extends BaseRenderer {
  // 继承BaseRenderer
  constructor(eventBus, bpmnRenderer) {
    super(eventBus, HIGH_PRIORITY);
    this.bpmnRenderer = bpmnRenderer;
  }

  canRender(element) {
    // ignore labels
    return !element.labelTarget;
  }

  drawShape(parentNode, element) {
    if (element.type === 'bpmn:UserTask') {
      const nodeType = getNodeType(element);
      if (nodeType === 'monitorTask') {
        const type = element.type; // 获取到类型
        if (customElements.includes(type)) {
          // 在这里创建了一个image
          const customEle = this.bpmnRenderer.handlers['bpmn:Task'](
            parentNode,
            element
          );
          const { url, attr } = customConfig[nodeType];
          const customIcon = svgCreate('image', { ...attr, href: url });

          svgAppend(parentNode, customIcon);

          return customEle;
        }
      }
    } else if (element.type === 'bpmn:ServiceTask') {
      const nodeType = getNodeType(element);
      if (nodeType === 'notificationTask') {
        const type = element.type; // 获取到类型
        if (customElements.includes(type)) {
          // 在这里创建了一个image
          const customEle = this.bpmnRenderer.handlers['bpmn:Task'](
            parentNode,
            element
          );
          const { url, attr } = customConfig[nodeType];
          const customIcon = svgCreate('image', { ...attr, href: url });
          svgAppend(parentNode, customIcon);
          return customEle;
        }
      }
    } else if (element.type === 'bpmn:StartEvent') {
      const customEle = this.bpmnRenderer.handlers['bpmn:Event'](
        parentNode,
        element,
        {}
      );
      const { url, attr } = customConfig['startEvent'];
      const customIcon = svgCreate('image', { ...attr, href: url });
      svgAppend(parentNode, customIcon);
      return customEle;
    } else if (element.type === 'bpmn:EndEvent') {
      const customEle = this.bpmnRenderer.handlers['bpmn:Event'](
        parentNode,
        element,
        {}
      );
      const { url, attr } = customConfig['endEvent'];
      const customIcon = svgCreate('image', { ...attr, href: url });
      svgAppend(parentNode, customIcon);
      return customEle;
    }
    const shape = this.bpmnRenderer.drawShape(parentNode, element);
    return shape;
  }

  getShapePath(shape) {
    return this.bpmnRenderer.getShapePath(shape);
  }
}

CustomRenderer.$inject = ['eventBus', 'bpmnRenderer'];
