




















































































































































import { Vue, Component } from 'vue-property-decorator';
import BaseChart from '@components/BaseChart/index.vue';
import BusinessLeaderPost from './businessLeaderPost.vue';
import moment from 'moment';
import { currencyFormatter } from '@utils/util';
import {
  queryTodayTaskProgress,
  queryProductNumber,
  queryProductStock,
  queryKeyTaskProgress,
  queryTodyProductCompleted,
  queryKeyTaskList
} from '@api/moreDimensionMonitor.js';

@Component({
  components: {
    BaseChart,
    BusinessLeaderPost
  }
})
export default class DepartmentChargePage extends Vue {
  private currentTab: any = 1;
  private taskSelectDate: any = '';
  private keyTaskList: any = [];
  private keyTaskSelect: any = ['001'];
  private keyTaskBar: any = {};
  private currentTime: any = '';
  private todayTaskBar: any = {};
  private productNumberForm = 'chart';
  private productNumberTableData: any = [];
  private productNumberTableColumns: any = [
    {
      prop: 'productName',
      label: '产品',
      minWidth: '120',
      'show-overflow-tooltip': true
    },
    { prop: 'productNum', label: '运作产品', align: 'center' },
    { prop: 'productNewAddNum', label: '当日新增', align: 'center' }
  ];
  private productNumberTableLoading = true;
  private productNumberBar: any = {};
  private productStockLine: any = {};
  private todyProductBar: any = {};

  // 关键任务完成进度数据获取
  getKeyTaskProgress() {
    if (!this.keyTaskSelect.length) {
      return this.$message.error('请选择关键任务');
    }
    if (!this.taskSelectDate) {
      return this.$message.error('请选择查询日期');
    }
    const params = {
      endDate: this.taskSelectDate[1],
      startDate: this.taskSelectDate[0],
      taskIds: this.keyTaskSelect
    };
    queryKeyTaskProgress(params).then(
      ({ data, error }: { data: any; error: any }) => {
        if (!error) {
          if (data.length) {
            const xAxis = data.map((item: any) => {
              return item.taskTime;
            });
            const seriesData: any = [];
            data.forEach((item: any, itemIndex: any) => {
              item.dayKeyTaskTimeProgresses.forEach((node: any, index: any) => {
                const value = data[0].taskTime + ' ' + node.lastCompleteTime;
                if (seriesData[index]) {
                  seriesData[index].data[itemIndex] = value;
                } else {
                  const obj = {
                    name: node.taskName,
                    showSymbol: false,
                    data: [value],
                    type: 'line'
                  };
                  this.$set(seriesData, index, obj);
                }
              });
            });
            this.keyTaskBar = {
              xAxis: {
                axisLine: {
                  lineStyle: {
                    color: '#E1E4EB'
                  }
                },
                axisLabel: {
                  color: '#474C5C'
                },
                data: xAxis
              },
              yAxis: {
                type: 'time',
                axisLine: {
                  show: false
                },
                axisTick: {
                  show: false
                },
                splitLine: {
                  lineStyle: {
                    color: '#EBECF0',
                    type: 'dashed'
                  }
                },
                axisLabel: {
                  formatter: function(value) {
                    return moment(value)
                      .format('YYYY-MM-DD HH:mm')
                      .substr(11, 20);
                  }
                }
              },
              legend: {
                show: false
              },
              grid: {
                // left: '5%',
                // right: '5%',
                top: '6%',
                bottom: '8%',
                containLabel: true
              },
              tooltip: {
                formatter: (params) => {
                  const list = params.map((item) => {
                    const value = item.value.split(' ')[1];
                    return item.marker + ' ' + item.seriesName + '： ' + value;
                  });
                  list.unshift(params[0].axisValue);
                  return list.join('<br>');
                },
                textStyle: {
                  align: 'left'
                }
              },
              series: seriesData,
              dataZoom: [
                {
                  type: 'slider',
                  height: 10,
                  // left: '10%',
                  // right: '12%',
                  bottom: 0,
                  maxValueSpan: 10,
                  showDetail: false,
                  handleIcon:
                    'M10.7,11.9H9.3c-4.9,0.3-8.8,4.4-8.8,9.4c0,5,3.9,9.1,8.8,9.4h1.3c4.9-0.3,8.8-4.4,8.8-9.4C19.5,16.3,15.6,12.2,10.7,11.9z M13.3,24.4H6.7V23h6.6V24.4z M13.3,19.6H6.7v-1.4h6.6V19.6z'
                }
              ],
              animation: false
            };
          }
        } else {
          this.$message.error('关键任务完成进度查询失败：' + error.message);
        }
      }
    );
  }
  // 关键任务完成进度 -- 时间选择
  handleChangeTaskDate() {
    this.getKeyTaskProgress();
  }
  // 关键任务完成进度 -- 关键任务选择
  handleChangeKeyTask(e) {
    if (!e) {
      this.getKeyTaskProgress();
    }
  }
  // 当日关键任务完成进度 获取
  getTodayTaskProgress() {
    queryTodayTaskProgress({ taskTime: this.currentTime }).then(
      ({ data, error }: { data: any; error: any }) => {
        if (!error) {
          const yAxis: any = [];
          const yAxis2: any = [];
          const completedList: any = [];
          const incompleteList: any = [];
          data.forEach((item: any) => {
            yAxis.push(item.taskName);
            yAxis2.push(item.totalNum);
            completedList.push({
              value: (item.finishedNum / item.totalNum) * 100,
              count: item.finishedNum
            });
            incompleteList.push({
              value: (item.unfinishedNum / item.totalNum) * 100,
              count: item.unfinishedNum
            });
          });
          this.todayTaskBar = {
            yAxis: [
              {
                type: 'category',
                axisLine: {
                  show: false
                },
                axisTick: {
                  alignWithLabel: true,
                  lineStyle: {
                    color: '#E1E4EB'
                  }
                },
                axisLabel: {
                  color: '#474C5C'
                },
                data: yAxis
              },
              {
                type: 'category',
                name: '总数',
                axisLine: {
                  show: false
                },
                axisTick: {
                  show: false
                },
                splitLine: {
                  show: false
                },
                nameTextStyle: {
                  padding: [0, -36, 0, 0]
                },
                nameGap: -15,
                data: yAxis2
              }
            ],
            xAxis: [
              {
                type: 'value',
                axisLine: {
                  lineStyle: {
                    color: '#E1E4EB'
                  }
                },
                splitLine: {
                  lineStyle: {
                    color: '#EBECF0',
                    type: 'dashed'
                  }
                },
                axisLabel: {
                  color: '#474C5C',
                  formatter: (value) => {
                    return value + '%';
                  }
                },
                data: yAxis2
              }
            ],
            tooltip: {
              formatter: (params) => {
                // console.log(params)
                const list = params.map((item) => {
                  return (
                    item.marker +
                    ' ' +
                    item.seriesName +
                    '： ' +
                    currencyFormatter(item.value) +
                    '%'
                  );
                });
                list.unshift(params[0].axisValue);
                return list.join('<br>');
              }
            },
            series: [
              {
                name: '已完成',
                data: completedList,
                stack: 'progress',
                type: 'bar',
                label: {
                  show: true,
                  position: 'insideLeft',
                  formatter: (item) => {
                    return item.data.count;
                  }
                },
                barMaxWidth: 16
              },
              {
                name: '未完成',
                data: incompleteList,
                stack: 'progress',
                type: 'bar',
                label: {
                  show: true,
                  position: 'insideRight',
                  formatter: (item) => {
                    return item.data.count;
                  }
                },
                barMaxWidth: 16
              }
            ],
            color: ['#70B82D', '#E1E4EB']
          };
        } else {
          this.$message.error('当日关键任务完成进度查询失败：' + error.message);
        }
      }
    );
  }
  // 当日关键任务完成进度 时间选择
  handleKeyTaskTime() {
    this.getTodayTaskProgress();
  }
  //
  changeShowForm() {
    this.productNumberForm =
      this.productNumberForm === 'chart' ? 'table' : 'chart';
  }
  // 产品数量统计查询
  getProductNumber() {
    this.productNumberTableLoading = true;
    queryProductNumber().then(({ data, error }: { data: any; error: any }) => {
      if (!error) {
        this.productNumberTableData = data;
        this.productNumberTableLoading = false;
        const xAxis: any = [];
        const operateNumber: any = [];
        const todayIncreased: any = [];
        let operateTotal = 0;
        let increasedTotal = 0;
        data.forEach((item: any) => {
          xAxis.push(item.productName);
          operateNumber.push(item.productNum);
          todayIncreased.push(item.productNewAddNum);
          operateTotal += item.productNum;
          increasedTotal += item.productNewAddNum;
        });
        xAxis.unshift('合计');
        operateNumber.unshift(operateTotal);
        todayIncreased.unshift(increasedTotal);
        this.productNumberBar = {
          xAxis: {
            data: xAxis,
            axisLabel: {
              formatter: (value) => {
                // console.log(value);
                let str = '';
                const num = 10; //每行显示字数
                const valLength = value.length; //该项x轴字数
                const rowNum = Math.ceil(valLength / num); // 行数

                if (rowNum > 1) {
                  for (let i = 0; i < rowNum; i++) {
                    let temp = '';
                    const start = i * num;
                    const end = start + num;

                    temp = value.substring(start, end) + '\n';
                    str += temp;
                  }
                  return str;
                } else {
                  return value + '\n' + '\n';
                }
              }
            }
          },
          yAxis: {
            max: (value) => {
              return value.max + 1;
            }
          },
          series: [
            {
              name: '运作产品',
              data: operateNumber,
              stack: 'product',
              type: 'bar',
              barMaxWidth: 20,
              barMinHeight: 10
            },
            {
              name: '当日新增',
              data: todayIncreased,
              stack: 'product',
              type: 'bar',
              barMaxWidth: 20,
              barMinHeight: 10
            }
          ],
          grid: {
            bottom: '4%',
            containLabel: true
          },
          dataZoom: {
            type: 'slider',
            maxValueSpan: 5,
            height: 10,
            align: 'center',
            // left: '5%',
            // right: '4%',
            bottom: 0,
            showDetail: false,
            handleIcon:
              'M10.7,11.9H9.3c-4.9,0.3-8.8,4.4-8.8,9.4c0,5,3.9,9.1,8.8,9.4h1.3c4.9-0.3,8.8-4.4,8.8-9.4C19.5,16.3,15.6,12.2,10.7,11.9z M13.3,24.4H6.7V23h6.6V24.4z M13.3,19.6H6.7v-1.4h6.6V19.6z'
          },
          animation: false
        };
      } else {
        this.$message.error('产品数量统计查询失败: ' + error.message);
      }
    });
  }
  // 产品增量/存量获取
  getProductStock() {
    queryProductStock().then(({ data, error }: { data: any; error: any }) => {
      if (!error) {
        this.productStockLine = {
          series: [
            {
              name: '产品增量/存量',
              data: data.map((item: any) => {
                return item.totalNum;
              }),
              type: 'line',
              showSymbol: false
            }
          ],
          legend: {
            show: false
          },
          xAxis: {
            data: data.map((item: any) => {
              return item.month;
            })
          }
        };
      } else {
        this.$message.error('产品增量/存量获取失败： ' + error.message);
      }
    });
  }
  // 当日产品完成情况查询
  getTodyProductCompleted() {
    queryTodyProductCompleted().then(
      ({ data, error }: { data: any; error: any }) => {
        if (!error) {
          // const tempDate = '2020-02-02'
          const yAxis: any = data.map((item: any) => {
            return item.taskName;
          });
          const targetTime: any = data.map((item: any) => {
            return item.targetTime;
          });
          const allCompletdTime: any = data.map((item: any) => {
            return item.lastTime;
          });
          this.todyProductBar = {
            yAxis: [
              {
                type: 'category',
                axisLine: {
                  lineStyle: {
                    color: '#E1E4EB'
                  }
                },
                axisLabel: {
                  color: '#474C5C'
                },
                axisTick: {
                  alignWithLabel: true
                },
                data: yAxis
              }
            ],
            xAxis: {
              type: 'time',
              axisLabel: {
                color: '#474C5C',
                formatter: function(value) {
                  return moment(value)
                    .format('YYYY-MM-DD HH:mm')
                    .substr(11, 20);
                }
              },
              axisLine: {
                show: false
              },
              axisTick: {
                show: false
              },
              splitLine: {
                lineStyle: {
                  color: '#EBECF0',
                  type: 'dashed'
                }
              },
              min: (value) => {
                return value.min - 30 * 60 * 1000;
              }
            },
            tooltip: {
              formatter: (params) => {
                // console.log(params)
                const list = params.map((item) => {
                  const value = item.value.split(' ')[1];
                  return item.marker + ' ' + item.seriesName + '： ' + value;
                });
                list.unshift(params[0].axisValue);
                return list.join('<br>');
              }
            },
            grid: {
              right: '2%'
            },
            series: [
              {
                type: 'bar',
                name: '目标时间',
                data: targetTime,
                barMaxWidth: 12
              },
              {
                type: 'bar',
                name: '全部完成时间',
                data: allCompletdTime,
                barMaxWidth: 12
              }
            ],
            color: ['#2F6AFF', 'rgba(47,106,255,0.30)']
          };
        } else {
          this.$message.error('当日产品完成情况查询失败：' + error.message);
        }
      }
    );
  }
  // 关键任务列表获取
  getKeyTaskList() {
    queryKeyTaskList().then(({ data, error }: { data: any; error: any }) => {
      if (!error) {
        this.keyTaskList = data;
        if (data.length) {
          this.keyTaskSelect = [data[0].taskId];
          this.getKeyTaskProgress();
        }
      } else {
        this.$message.error('关键任务列表获取失败：' + error.message);
      }
    });
  }
  //面板切换
  handleSearch() {
    if (this.currentTab === 2) {
      this.init();
    }
  }
  init() {
    this.getKeyTaskList();
    this.getTodayTaskProgress();
    this.getProductNumber();
    this.getProductStock();
    this.getTodyProductCompleted();
  }

  private mounted() {
    this.currentTime = moment().format('HH:mm:ss');
    this.taskSelectDate = [
      moment()
        .subtract(7, 'days')
        .format('YYYY-MM-DD'),
      moment().format('YYYY-MM-DD')
    ];
    if (this.currentTab === 2) {
      this.init();
    }
  }
}
