<template>
  <!-- 简单日历 -->
  <section class="wh_container">
    <div class="wh_content_all">
      <div style="overflow:hidden;">
        <div
          class="wh_top_changge"
          style="width:40%;float:left;margin-left:20px;"
        >
          <li @click="PreYear(myDate, false)">
            <div class="wh_jiantou1"></div>
            <div class="wh_jiantou1"></div>
          </li>
          <li @click="PreMonth(myDate, false)">
            <div class="wh_jiantou1"></div>
          </li>
          <li class="wh_content_li">{{ dateTop }}</li>
          <li @click="NextMonth(myDate, false)">
            <div class="wh_jiantou2"></div>
          </li>
          <li @click="NextYear(myDate, false)">
            <div class="wh_jiantou2"></div>
            <div class="wh_jiantou2"></div>
          </li>
        </div>
        <div class="wh_top_changge" style="width:50%;float:right">
          <li>
            <span
              class="img_success"
              style="float: right;position:relative;right:5%"
            >
              <span class="open_img"></span>
              <span>开放</span>
            </span>
            <span
              class="img_success"
              style="float: right;position:relative;right:-8%"
            >
              <span class="half-day-holiday">
                <span class="half-day-circle"></span>
              </span>
              <span>半日休</span>
            </span>
            <span
              class="img_success"
              style="float: right;position:relative;right:-10%"
            >
              <span class="hollday_img"></span>
              <span>休市</span>
            </span>
          </li>
        </div>
      </div>
      <!-- 周一至周日 -->
      <div class="wh_content">
        <div class="wh_content_item" v-for="tag in textTop" :key="tag">
          <div class="wh_top_tag">{{ tag }}</div>
        </div>
      </div>
      <!-- 日期 -->
      <div class="wh_content">
        <div
          class="wh_content_item wh_content_item-two"
          style="cursor:pointer;"
          :style="chooseStyle(index, item)"
          v-for="(item, index) in dateSelect"
          :key="index"
          @click="clickDay(item, index)"
        >
          <!-- 休息 -->
          <div v-if="item.dayType === '0' && item.marketList.length > 0">
            <el-badge :class="changeColor ? 'item2' : ''">
              <el-popover
                placement="top-start"
                title=""
                width="80"
                trigger="hover"
                content=""
              >
                <p v-for="ele in item.marketList" :key="ele">
                  <span v-if="ele.openFlag === '0'" class="circle-zero"></span>
                  <span v-else-if="ele.openFlag === '1'" class="circle-one">
                    <span class="circle-one-child"></span>
                  </span>
                  <span v-else class="circle-two"></span>
                  <span>{{ ele.market }}</span>
                </p>
                <div
                  slot="reference"
                  class="wh_item_date"
                  v-bind:class="[
                    { wh_isMark: item.isMark },
                    { wh_other_dayhide: item.otherMonth !== 'nowMonth' },
                    { wh_want_dayhide: item.dayHide },
                    { wh_isToday: item.isToday },
                    { wh_chose_day: item.chooseDay },
                    setClass(item)
                  ]"
                  >{{ item.id }}</div
                ></el-popover
              >
              <div
                :class="
                  index % 7 === 0 || index % 7 === 6 ? '' : 'Holiday_primary'
                "
              ></div>
            </el-badge>
          </div>
          <div v-else-if="item.dayType === '0' && item.marketList.length === 0">
            <el-badge :class="changeColor ? 'item2' : ''">
              <div
                :class="
                  index % 7 === 0 || index % 7 === 6 ? '' : 'Holiday_primary'
                "
              ></div>
            </el-badge>
          </div>
          <!-- 工作日 -->
          <div v-else-if="item.dayType === '1' && item.marketList.length > 0">
            <el-badge
              :class="changeColor ? 'item item2' : 'item'"
              type="warning"
            >
              <el-popover
                placement="top-start"
                title=""
                width="80"
                trigger="hover"
                content=""
              >
                <p v-for="ele in item.marketList" :key="ele">
                  <span v-if="ele.openFlag === '0'" class="circle-zero"></span>
                  <span v-else-if="ele.openFlag === '1'" class="circle-one">
                    <span class="circle-one-child"></span>
                  </span>
                  <span v-else class="circle-two"></span
                  ><span>{{ ele.market }}</span></p
                >
                <div
                  slot="reference"
                  class="wh_item_date"
                  v-bind:class="[
                    { wh_isMark: item.isMark },
                    { wh_other_dayhide: item.otherMonth !== 'nowMonth' },
                    { wh_want_dayhide: item.dayHide },
                    { wh_isToday: item.isToday },
                    { wh_chose_day: item.chooseDay },
                    setClass(item)
                  ]"
                  >{{ item.id }}</div
                ></el-popover
              >
            </el-badge>
          </div>
          <div v-else>
            <el-badge
              :class="changeColor ? 'item item2' : 'item'"
              type="warning"
            >
              <div
                class="wh_item_date"
                v-bind:class="[
                  { wh_isMark: item.isMark },
                  { wh_other_dayhide: item.otherMonth !== 'nowMonth' },
                  { wh_want_dayhide: item.dayHide },
                  { wh_isToday: item.isToday },
                  { wh_chose_day: item.chooseDay },
                  setClass(item)
                ]"
                >{{ item.id }}</div
              >
            </el-badge>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import { getCalendarList } from '../../../../../api/multidimensionalMonitoring';
import timeUtil from './calendar.js';
import moment from 'moment';
export default {
  data() {
    return {
      bok: true,
      myDate: [],
      list: [],
      weekendList: [],
      historyChose: [],
      dateTop: '',
      dataChange: '',
      dateList: [],
      dateSelect: [],
      changeColor: false,
      memoList: [],
      dataList: [],
      currentTime: '',
      monthStart: '',
      monthEnd: ''
    };
  },
  props: {
    defaultChecked: {
      type: Array,
      default: () => []
    },
    markDate: {
      type: Array,
      default: () => []
    },

    markDateMore: {
      type: Array,
      default: () => []
    },
    textTop: {
      type: Array,
      default: () => ['日', '一', '二', '三', '四', '五', '六']
    },
    sundayStart: {
      type: Boolean,
      default: () => false
    },
    agoDayHide: {
      type: String,
      default: `0`
    },
    futureDayHide: {
      type: String,
      default: `2554387200`
    },
    partInfo: {
      type: String,
      default: ''
    }
  },
  created() {
    this.myDate = new Date();
    this.intStart();
    this.getList(this.myDate);
  },
  methods: {
    // 日期选中切换
    changeDate() {
      this.list.forEach((value, key) => {
        let dateValue = [];
        dateValue = value.date.split('/');
        if (dateValue[1] < 10) {
          dateValue[1] = '0' + dateValue[1];
        }
        if (dateValue[2] < 10) {
          dateValue[2] = '0' + dateValue[2];
        }
        dateValue = dateValue.join('-');
        this.dateList.forEach((val, k) => {
          if (val.date === dateValue) {
            this.list[key] = Object.assign(val, value);
            return;
          }
        });
      });
      this.dateSelect = this.list;
    },

    // 每月披露状态
    monthlyStateQueryDate(arr) {
      const _this = this;

      const params = {
        market: this.defaultChecked,
        range: [this.monthStart, this.monthEnd]
      };

      this.deleteEmptyParam(params);
      getCalendarList(params).then(({ data, error }) => {
        this.dateList = data;
        const selectionArr = [];
        const selectionArrId = [];
        if (this.dateList && this.dateList.length > 0) {
          this.dateList.forEach((ele) => {
            if (
              ele.date.split('-').join('') ===
              this.dataChange.split('-').join('')
            ) {
              this.memoList = ele.memoList;
            }
          });
        }

        if (this.memoList && this.memoList.length > 0) {
          this.memoList.forEach((item) => {
            item.isEdit = true;
            if (item.eventStatus === '1') {
              selectionArr.push(item);
              selectionArrId.push(item.id);
            }
          });
        }
        this.dataList = this.memoList;
        this.bok = true;
        if (this.bok) {
          this.$emit('getVal', this.dataList, selectionArr, this.dataChange);
          this.bok = false;
        }

        this.changeDate();
      });
    },
    // 判断参数对象里面有没有空值
    deleteEmptyParam(value) {
      for (const key in value) {
        if (
          value[key] === '' ||
          value[key] === null ||
          value[key] === undefined ||
          value[key] === [] ||
          value[key].length === 0
        ) {
          delete value[key];
        }
      }
    },
    // 初始化起始日期
    intStart() {
      timeUtil.sundayStart = this.sundayStart;
    },
    // 日历自定义样式添加
    setClass(data) {
      const obj = {};
      obj[data.markClassName] = data.markClassName;
      return obj;
    },
    // 改变周六日显示颜色
    chooseStyle(index, item) {
      let s2 = '';
      const s1 = [0, 1, 2, 3, 4, 5];
      s1.forEach((items) => {
        if (items * 7 === index || items * 7 + 6 === index) {
          s2 = 'color:#9AC9FF';
        }
      });
      return s2;
    },
    // 日历点击
    clickDay: function(item, index) {
      this.memoList = [];
      this.bok = true;
      this.$emit('hoverDay', item);
      if (item.otherMonth === 'nowMonth' && !item.dayHide) {
        this.getList(this.myDate, item.date);
      }
      if (item.otherMonth !== 'nowMonth') {
        item.otherMonth === 'preMonth'
          ? this.PreMonth(item.date)
          : this.NextMonth(item.date);
      }
      const temDate = item.date.split('/');
      if (temDate[1] < 10) {
        temDate[1] = '0' + temDate[1];
      }
      if (temDate[2] < 10) {
        temDate[2] = '0' + temDate[2];
      }
      this.dataChange = temDate.join('-');
      this.monthlyStateQueryDate();
    },
    // 月份切换
    ChoseMonth: function(date, isChosedDay = true) {
      date = timeUtil.dateFormat(date);
      this.myDate = new Date(date);
      this.$emit('changeMonth', timeUtil.dateFormat(this.myDate));
      if (isChosedDay) {
        this.getList(this.myDate, date, isChosedDay);
      } else {
        this.getList(this.myDate);
      }
    },
    // 上一年
    PreYear: function(date, isChosedDay = true) {
      date = timeUtil.dateFormat(date);
      this.myDate = timeUtil.getOtherYear(this.myDate, 'PreYear');
      this.$emit('changeYear', timeUtil.dateFormat(this.myDate));
      if (isChosedDay) {
        this.getList(this.myDate, date, isChosedDay);
      } else {
        this.getList(this.myDate);
      }
    },
    // 下一年
    NextYear: function(date, isChosedDay = true) {
      date = timeUtil.dateFormat(date);
      this.myDate = timeUtil.getOtherYear(this.myDate, 'nextYear');
      this.$emit('changeYear', timeUtil.dateFormat(this.myDate));
      if (isChosedDay) {
        this.getList(this.myDate, date, isChosedDay);
      } else {
        this.getList(this.myDate);
      }
    },
    // 上一月
    PreMonth: function(date, isChosedDay = true) {
      this.monthStart = moment(date)
        .startOf('month')
        .format('YYYY-MM-DD');
      this.monthEnd = moment(date)
        .endOf('month')
        .format('YYYY-MM-DD');
      date = timeUtil.dateFormat(date);
      this.myDate = timeUtil.getOtherMonth(this.myDate, 'preMonth');
      this.$emit('changeMonth', timeUtil.dateFormat(this.myDate));
      if (isChosedDay) {
        this.getList(this.myDate, date, isChosedDay);
      } else {
        this.getList(this.myDate);
      }
    },
    // 下一月
    NextMonth: function(date, isChosedDay = true) {
      this.monthStart = moment(date)
        .startOf('month')
        .format('YYYY-MM-DD');
      this.monthEnd = moment(date)
        .endOf('month')
        .format('YYYY-MM-DD');
      date = timeUtil.dateFormat(date);
      this.myDate = timeUtil.getOtherMonth(this.myDate, 'nextMonth');
      this.$emit('changeMonth', timeUtil.dateFormat(this.myDate));
      if (isChosedDay) {
        this.getList(this.myDate, date, isChosedDay);
      } else {
        this.getList(this.myDate);
      }
    },
    // 日历参数格式化
    forMatArgs: function() {
      let markDate = this.markDate;
      let markDateMore = this.markDateMore;
      markDate = markDate.map((k) => {
        return timeUtil.dateFormat(k);
      });
      markDateMore = markDateMore.map((k) => {
        k.date = timeUtil.dateFormat(k.date);
        return k;
      });
      return [markDate, markDateMore];
    },
    // 获取当前日历状态
    getList: function(date, chooseDay, isChosedDay = true) {
      this.monthStart = moment(date)
        .startOf('month')
        .format('YYYY-MM-DD');
      this.monthEnd = moment(date)
        .endOf('month')
        .format('YYYY-MM-DD');
      const [markDate, markDateMore] = this.forMatArgs();
      this.dateTop = `${date.getFullYear()}年${date.getMonth() + 1}月`;
      const month =
        date.getMonth() + 1 < 10
          ? '0' + (date.getMonth() + 1)
          : date.getMonth() + 1;
      const day = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
      this.dataChange = `${date.getFullYear()}-${month}-${day}`;
      const arr = timeUtil.getMonthList(this.myDate);
      for (let i = 0; i < arr.length; i++) {
        let markClassName = '';
        const k = arr[i];
        k.chooseDay = false;
        const nowTime = k.date;
        const t = new Date(nowTime).getTime() / 1000;
        // 看每一天的class
        for (const c of markDateMore) {
          if (c.date === nowTime) {
            markClassName = c.className || '';
          }
        }
        // 标记选中某些天 设置class
        k.markClassName = markClassName;
        k.isMark = markDate.indexOf(nowTime) > -1;
        // 无法选中某天
        k.dayHide = t < this.agoDayHide || t > this.futureDayHide;
        if (k.isToday) {
          this.$emit('isToday', nowTime);
        }
        const flag = !k.dayHide && k.otherMonth === 'nowMonth';
        if (chooseDay && chooseDay === nowTime && flag) {
          this.$emit('choseDay', nowTime);
          this.historyChose.push(nowTime);
          k.chooseDay = true;
        } else if (
          this.historyChose[this.historyChose.length - 1] === nowTime &&
          !chooseDay &&
          flag
        ) {
          k.chooseDay = true;
        }
      }
      this.list = arr;
      this.monthlyStateQueryDate();
    }
  },
  watch: {
    // 日选中
    markDate: {
      handler(val, oldVal) {
        this.getList(this.myDate);
      },
      deep: true
    },
    // 日详情选中
    markDateMore: {
      handler(val, oldVal) {
        this.getList(this.myDate);
      },
      deep: true
    },
    // 过去日期更新
    agoDayHide: {
      handler(val, oldVal) {
        this.getList(this.myDate);
      },
      deep: true
    },
    // 未来日期更新
    futureDayHide: {
      handler(val, oldVal) {
        this.getList(this.myDate);
      },
      deep: true
    },
    // 周末显示位置变更
    sundayStart: {
      handler(val, oldVal) {
        this.intStart();
        this.getList(this.myDate);
      },
      deep: true
    },
    partInfo: {
      handler(val, oldVal) {
        if (val || oldVal) {
          this.myDate = new Date();
          this.intStart();
          this.getList(this.myDate);
        }
      },
      deep: true
    }
  }
};
</script>

<style lang="scss" scoped>
* {
  margin: 0;
  padding: 0;
}
.wh_container {
  max-width: 750px;
  margin: auto;
}

li {
  list-style-type: none;
}
.wh_top_changge {
  display: flex;
}

.wh_top_changge li {
  cursor: pointer;
  display: flex;
  color: #fff;
  font-size: 18px;
  flex: 1;
  justify-content: center;
  align-items: center;
  height: 47px;
}

.wh_top_changge .wh_content_li {
  cursor: auto;
  flex: 2.5;
  font-size: 16px;
  color: $color-main;
}
.wh_content_all {
  font-family: -apple-system, BlinkMacSystemFont, 'PingFang SC',
    'Helvetica Neue', STHeiti, 'Microsoft Yahei', Tahoma, Simsun, sans-serif;
  // background-color: #0fc37c;
  width: 100%;
  overflow: hidden;
  padding-bottom: 8px;
}

.wh_content {
  display: flex;
  flex-wrap: wrap;
  padding: 0 3% 0 3%;
  width: 100%;
}

.wh_content:first-child .wh_content_item_tag,
.wh_content:first-child .wh_content_item {
  color: #ddd;
  font-size: 16px;
}

.wh_content_item,
wh_content_item_tag {
  font-size: 14px;
  width: 13.4%;
  text-align: center;
  color: #3c4455;
  position: relative;
}
.wh_content_item {
  height: 40px;
}
.wh_content_item-two {
  .wh_chose_day {
    border: 2px solid $color-main;
    border-radius: 50%;
  }
}

.wh_top_tag {
  width: 40px;
  height: 40px;
  line-height: 40px;
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  color: #663c4455;
}

.wh_item_date {
  width: 20px;
  height: 20px;
  // line-height: 40px;
  margin: auto;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
}

.wh_jiantou1 {
  width: 6px;
  height: 6px;
  border-top: 2px solid $color-main;
  border-left: 2px solid $color-main;
  transform: rotate(-45deg);
}

.wh_jiantou1:active,
.wh_jiantou2:active {
  border-color: #2a76cd;
}

.wh_jiantou2 {
  width: 6px;
  height: 6px;
  border-top: 2px solid $color-main;
  border-right: 2px solid $color-main;
  transform: rotate(45deg);
}
.wh_content_item > .wh_isMark {
  margin: auto;
  border-radius: 100px;
  background: blue;
  z-index: 2;
}
.wh_content_item .wh_other_dayhide {
  color: #bfbfbf;
}
.wh_content_item .wh_want_dayhide {
  color: #bfbfbf;
}
.wh_content_item .wh_isToday {
  background: #2f6aff;
  border-radius: 6px;
  color: #ffffffff;
}
</style>
