












































































































































































































































































import { Vue, Component } from 'vue-property-decorator';
import {
  getTaskNumber,
  showResults,
  getProcessMonitorList,
  sendTemplateEmail,
  getProcessType
} from '../../../api/multidimensionalMonitoring.js';
import Calendar from './components/calendar';
import echarts from 'echarts';
import moment from 'moment';
import BaseChart from '@components/BaseChart/index.vue';
import BusinessLeaderPost from './businessLeaderPost.vue';
@Component({
  components: { Calendar, BaseChart, BusinessLeaderPost }
})
export default class EmployeePosition extends Vue {
  private stepInfo = {
    list: []
  };
  private elTagText = '全部';
  private processType: Array<any> = [];
  private pieChart = null;
  private brokenChart = null;

  private param: any = {
    type: '1'
  };
  private changeShowWork = 1;
  private activeName = 'all';

  private loading = false;

  private timer: any = null;
  private showMoreBtn = false;
  private zhexianData: Array<any> = [];
  private zhexianNumber: any = {};
  private currentNodeInfo: any = {};
  private resultData: any = {}; //工作完成情况展示
  private actionFlage: any = 1;
  private processState: any = 2;
  private currentTime: any = ''; //当前时间
  private form: any = {
    checkList: ''
  };
  private sponsorList: Array<any> = [
    {
      label: '我发起',
      value: 1
    },
    {
      label: '我经办',
      value: 2
    }
  ];
  private processStatusList: Array<any> = [
    {
      label: '已超时',
      value: 1
    },
    {
      label: '按时',
      value: 2
    }
  ];
  private total = 0;
  private page = 1;
  private limit = 10;
  private pieOptions: any = [];
  private currentTab: any = 1;
  getBrokenOptions(val, val2, val3) {
    const data = [
      { value: val3.pend, name: '待办任务' },
      { value: val3.done, name: '已办任务' },
      { value: val3.timeout, name: '超时任务' }
    ];

    const brokenOptions: any = {
      dataZoom: {
        type: 'slider',
        height: 10,
        maxValueSpan: 12,
        bottom: '0',
        right: '9%',
        left: '9%',
        handleIcon:
          'M10.7,11.9H9.3c-4.9,0.3-8.8,4.4-8.8,9.4c0,5,3.9,9.1,8.8,9.4h1.3c4.9-0.3,8.8-4.4,8.8-9.4C19.5,16.3,15.6,12.2,10.7,11.9z M13.3,24.4H6.7V23h6.6V24.4z M13.3,19.6H6.7v-1.4h6.6V19.6z'
      },
      tooltip: {
        trigger: 'axis'
      },
      legend: {
        icon: 'circle',
        itemWidth: 8,
        top: 10,
        left: 13,
        selected: {
          超时任务: true
        },
        data: [
          {
            name: '待办任务'
          },
          {
            name: '已办任务'
          },
          {
            name: '超时任务'
          }
        ],
        formatter: function(name) {
          let total = 0;
          let target;
          for (let i = 0, l = data.length; i < l; i++) {
            total += data[i].value;
            if (data[i].name == name) {
              target = data[i].value;
            }
          }
          let arr: any = [];
          if (name === '待办任务') {
            arr = ['{a|' + name + ' }{one|' + target + '}'];
          } else if (name === '已办任务') {
            arr = ['{a|' + name + '}{two|' + target + '}'];
          } else {
            arr = ['{a|' + name + '}{three|' + target + '}'];
          }

          return arr.join('\n');
        },
        textStyle: {
          rich: {
            a: {
              fontSize: 12,
              align: 'left',
              padding: [0, 0, 0, 0]
            },
            one: {
              fontSize: 20,
              align: 'right',
              padding: [0, 45, 0, 10],
              lineHeight: 25,
              color: '#6FB423'
            },
            two: {
              fontSize: 20,
              align: 'right',
              padding: [0, 40, 0, 10],
              lineHeight: 25,
              color: '#2F6AFF'
            },
            three: {
              fontSize: 20,
              align: 'right',
              padding: [0, 0, 0, 5],
              lineHeight: 25,
              color: '#DF5146'
            }
          }
        }
      },
      grid: {
        left: '3%',
        right: '4%',
        bottom: '7%',
        containLabel: true
      },
      xAxis: {
        type: 'category',
        boundaryGap: false,
        axisLine: {
          lineStyle: {
            color: '#95A4BC'
          }
        },
        data: val
      },
      yAxis: {
        type: 'value',
        splitLine: {
          lineStyle: {
            color: '#EBECF0',
            type: 'dashed'
          }
        },
        axisTick: {
          show: false
        },
        axisLine: {
          show: false,
          lineStyle: {
            color: '#95A4BC'
          }
        }
      },
      series: val2,
      color: ['#2F6AFF', '#6FB423', '#DF5146']
    };
    const brokenBox = echarts.init(document.getElementById('brokenBox') as any);
    brokenBox.setOption(brokenOptions);
  }
  stepMouseEnter(item, ref) {
    this.currentNodeInfo = item;

    this.$refs[ref][0].style.display = 'block';
  }
  stepMouseLeave(ref) {
    this.currentNodeInfo = {};
    this.$refs[ref][0].style.display = 'none';
  }
  getTaskNumber() {
    getTaskNumber().then(({ data }) => {
      if (data && data.line) {
        data.line.forEach((ele) => {
          ele.type = 'line';
          ele.symbol = 'none';
        });
        this.zhexianData = data.line;
        this.zhexianNumber = {
          done: data.done,
          pend: data.pend,
          timeout: data.timeout
        };
      }
    });
  }
  showResults() {
    const param = {
      type: this.changeShowWork
    };
    showResults(param).then(({ data }) => {
      if (data) {
        this.resultData = data;
        this.pieOptions = {
          tooltip: {
            trigger: 'item',
            formatter: '{a} <br/>{b}: {c} ({d}%)'
          },
          legend: {
            orient: 'horizontal',
            icon: 'circle',
            right: 0,
            top: 18,
            bottom: 10,
            selectedMode: false,
            data: ['正常处理', '超时处理']
          },
          series: [
            {
              center: ['45%', '53%'],
              name: '访问来源',
              type: 'pie',
              radius: ['0', '75%'],
              label: {
                normal: {
                  show: true,
                  position: 'outer',
                  formatter: '{d}% ',
                  textStyle: {
                    fontSize: 14,
                    color: '#474C5C'
                  }
                }
              },
              labelLine: {
                normal: {
                  lineStyle: {
                    color: '#469DEB'
                  },
                  length: 5
                }
              },
              emphasis: {
                label: {
                  show: true,
                  fontSize: '30',
                  fontWeight: 'bold'
                }
              },
              data: [
                { value: this.resultData.normal, name: '正常处理' },
                { value: this.resultData.timeout, name: '超时处理' }
              ]
            }
          ],
          color: ['#2F6AFF', '#6FBCFF']
        };
      }
    });
  }
  elTagChange(val) {
    this.elTagText = val;
    this.getProcessMonitorList();
  }
  setTimeInterval() {
    const t = new Date();
    const time = t.getHours();
    const array: any = [];
    for (let i = 0; i <= time; i++) {
      if (i < 10) {
        array.push('0' + i + ':00');
      } else {
        array.push(i + ':00');
      }
    }
    this.getBrokenOptions(array, this.zhexianData, this.zhexianNumber);
  }
  showMore() {
    this.showMoreBtn = !this.showMoreBtn;
  }
  getProcessMonitorList() {
    const param = {
      limit: this.limit,
      page: this.page,
      processType: this.elTagText,
      processState: this.processState,
      actionFlage: this.actionFlage,
      currentTime: this.currentTime
    };

    this.loading = true;
    getProcessMonitorList(param).then(({ data }) => {
      if (data) {
        this.loading = false;
        this.stepInfo.list = data.rows;

        this.total = data.total;
      }
    });
  }
  handleSizeChange(val) {
    this.page = 1;
    this.limit = val;
    this.getProcessMonitorList();
  }
  handleCurrentChange(val) {
    this.page = val;
    this.getProcessMonitorList();
  }
  changeActionFlage() {
    this.getProcessMonitorList();
  }
  handleChangeShowWork() {
    this.showResults();
  }
  changeProcessState(val) {
    this.processState = val;
    this.getProcessMonitorList();
  }
  sendEmail(item) {
    const param = {
      emailAddress: item.emailAddress,
      lastNodeFinishTime: item.lastNodeFinishTime,
      manager: item.manager,
      nodeName: item.nodeName,
      phoneNumber: item.phoneNumber,
      thisNodeFinishTime: item.thisNodeFinishTime
    };
    sendTemplateEmail(param).then(({ error }) => {
      if (!error) {
        this.$message({
          message: '邮件发送成功!',
          type: 'success'
        });
      } else {
        this.$message({
          message: `邮件发送失败：${error.message}`,
          type: 'error'
        });
      }
    });
  }
  init() {
    this.getTaskNumber();
    this.showResults();
    this.getProcessMonitorList();
    this.processTypeList();
    this.timer = setInterval(this.setTimeInterval, 1000);
  }
  //面板切换
  handleSearch() {
    if (this.currentTab === 2) {
      this.init();
    } else {
      clearInterval(this.timer);
    }
  }
  // 流程类型查询
  processTypeList() {
    getProcessType().then(({ data, error }) => {
      if (data) {
        this.processType = data;
      } else {
        this.$message({
          message: `流程类型查询失败: ${error.message}`,
          type: 'error'
        });
      }
    });
  }
  created() {
    this.currentTime = moment()
      .locale('zh-cn')
      .format('YYYY-MM-DD HH:mm');
  }
  destroyed() {
    clearInterval(this.timer);
    this.timer = null;
  }
}
