import $ajax from '@utils/ajax';
const serverUrl = $ajax.serverUrl;
// const serverUrlMock = $ajax.serverUrlMock;
const serverName = '/multidimensional-monitor/';

// 关键任务列表获取
export async function queryKeyTaskList() {
  return $ajax.get(serverUrl(`${serverName}departadmin/task-list`));
}

