













































































































































































































































































import { Vue, Component } from 'vue-property-decorator';
import {
  getprocesslist,
  analysisWorkList,
  completionRecentWorkList,
  progressCompletionList,
  memberDetailPendList,
  completionDayList,
  thisweekWorkloadList,
  getProgressCompletionList
} from '../../../api/multidimensionalMonitoring.js';
import echarts from 'echarts';
import moment from 'moment';
import BusinessLeaderPost from './businessLeaderPost.vue';
import BaseChart from '@components/BaseChart/index.vue';

@Component({
  components: {
    BaseChart,
    BusinessLeaderPost
  }
})
export default class CurrencyPanel extends Vue {
  private loading = false;
  private showVisibleRecently = false;
  private tableList: Array<any> = [];
  private analysiWorkData: Array<any> = [];
  private completionRecentWorkData: Array<any> = [];
  private progressCompletionData: Array<any> = [];
  private memberDetailPendData: Array<any> = [];
  private completionDayData: Array<any> = [];
  private thisweekWorkloadData: Array<any> = [];
  private changeicon = true;
  private nearDayDuration = 1;
  private nearWorkForm: any = 'chart';
  private empolyChange: any = 'chart';
  private progressOptions: any = [];
  private dayNormal = '';
  private dayTimeout = '';
  private dayOptions: any = [];
  private barOptions: any = [];
  private alreadyOptions: any = [];
  private currentTab: any = 1;
  private employeeAnalysisBar: any = {};
  private keyTaskList: any = [];
  private keyTaskSelect: any = [];
  columns() {
    const _this = this;
    return [
      {
        prop: 'name',
        label: '岗位',
        minWidth: 80,
        'show-overflow-tooltip': true
      },
      {
        prop: 'processStatus',
        label: '待办',
        minWidth: 50
      },
      {
        prop: 'urgentOfLevel',
        label: '已超时待办',
        width: 50
      },
      {
        prop: 'endTime',
        label: '待办-尽快',
        minWidth: 50,
        'show-overflow-tooltip': true
      },
      {
        prop: 'endTime',
        label: '待办-其他',
        minWidth: 80,
        'show-overflow-tooltip': true
      }
    ];
  }
  columnsAnalysiWork() {
    return [
      {
        prop: 'name',
        label: '名称',
        'show-overflow-tooltip': true
      },
      {
        prop: 'handledNum',
        label: '处理流程个数'
      },
      {
        prop: 'timeOutHandle',
        label: '超时处理'
      }
    ];
  }
  completionRecentWork() {
    return [
      {
        prop: 'date',
        label: '日期',
        'show-overflow-tooltip': true
      },
      {
        prop: 'finalCompletionTime',
        label: '最后完成时间'
      },
      {
        prop: 'completeStaff',
        label: '完成员工'
      },
      {
        prop: 'targetTime',
        label: '目标时间'
      }
    ];
  }
  progressCompletionWork() {
    return [
      {
        prop: 'name',
        label: '重点业务',
        'show-overflow-tooltip': true
      },
      {
        prop: 'newOfday',
        label: '当日新增'
      },
      {
        prop: 'unresolved',
        label: '未结业务'
      }
    ];
  }
  memberDetailPendWork() {
    return [
      {
        prop: 'name',
        label: '岗位',
        'show-overflow-tooltip': true
      },
      {
        prop: 'pending',
        label: '待办'
      },
      {
        prop: 'timeoutPend',
        label: '已超时待办'
      },
      {
        prop: 'quicklyPend',
        label: '待办-尽快'
      },
      {
        prop: 'otherPend',
        label: '待办其他'
      }
    ];
  }
  completionDayWork() {
    return [
      {
        type: 'expand',
        width: 55
      },
      {
        prop: 'workType',
        label: '工作类型',
        'show-overflow-tooltip': true
      },
      {
        prop: 'completed',
        label: '已完成'
      },
      {
        prop: 'incomplete',
        label: '未完成'
      },
      {
        prop: 'total',
        label: '总数'
      }
    ];
  }
  thisweekWorkloadWork() {
    return [
      {
        prop: 'name',
        label: '人员',
        'show-overflow-tooltip': true
      },
      {
        prop: 'normalHandle',
        label: '正常处理'
      },
      {
        prop: 'timeOutHandle',
        label: '超时处理'
      }
    ];
  }
  getList() {
    this.loading = true;
    getprocesslist().then(({ data }) => {
      if (data) {
        this.loading = false;
        this.tableList = data;
      }
    });
  }
  getAnalysiWork() {
    this.loading = true;
    analysisWorkList().then(({ data, error }) => {
      if (data) {
        this.loading = false;
        this.analysiWorkData = data;
        const xAxis: any = [];
        const handledNumList: any = [];
        const timeOutHandleList: any = [];
        data.forEach((item: any) => {
          xAxis.push(item.name);
          handledNumList.push(item.handledNum);
          timeOutHandleList.push(item.timeOutHandle);
        });
        this.employeeAnalysisBar = {
          legend: {
            right: '5%',
            top: '3%'
          },
          xAxis: {
            data: xAxis
          },
          series: [
            {
              type: 'bar',
              name: '流程处理个数',
              data: handledNumList,
              barMaxWidth: 16,
              barMinHeight: 10
            },
            {
              type: 'bar',
              name: '超时处理',
              data: timeOutHandleList,
              barMaxWidth: 16,
              barMinHeight: 10
            }
          ],
          grid: {
            left: '5%',
            bottom: '10%',
            right: '5%'
          },
          dataZoom: [
            {
              type: 'slider',
              height: 10,
              maxValueSpan: 4,
              bottom: '3%',
              right: '6%',
              showDetail: false,
              handleIcon:
                'M10.7,11.9H9.3c-4.9,0.3-8.8,4.4-8.8,9.4c0,5,3.9,9.1,8.8,9.4h1.3c4.9-0.3,8.8-4.4,8.8-9.4C19.5,16.3,15.6,12.2,10.7,11.9z M13.3,24.4H6.7V23h6.6V24.4z M13.3,19.6H6.7v-1.4h6.6V19.6z'
            }
          ],
          animation: false
        };
      } else {
        this.$message.error(`员工工作统计分析查询失败:${error.message}`);
      }
    });
  }
  // 重点业务完成进度
  getcompletionRecentWork() {
    const param = {
      type: this.nearDayDuration
    };

    completionRecentWorkList(param).then(({ data, error }) => {
      if (data) {
        this.loading = false;
        this.completionRecentWorkData = data.data;
        this.dayNormal = data.normal;
        this.dayTimeout = data.timeout;
        const date: any = [];
        const finalCompletionTime: any = [];
        const BASE_TIME = moment(new Date()).format('YYYY-MM-DD');
        const targetTime: any = BASE_TIME + ' ' + data.data[0].targetTime;

        data.data.forEach((item) => {
          finalCompletionTime.push({
            value: BASE_TIME + ' ' + item.finalCompletionTime,
            person: item.completeStaff
          });
          date.push(item.date);
        });
        this.alreadyOptions = {
          legend: {
            icon: 'circle',
            top: '0',
            right: '5%'
          },
          dataZoom: [
            {
              type: 'slider',
              height: 10,
              maxValueSpan: 7,
              bottom: 0,
              right: '7%',
              left: '7%',
              showDetail: false,
              handleIcon:
                'M10.7,11.9H9.3c-4.9,0.3-8.8,4.4-8.8,9.4c0,5,3.9,9.1,8.8,9.4h1.3c4.9-0.3,8.8-4.4,8.8-9.4C19.5,16.3,15.6,12.2,10.7,11.9z M13.3,24.4H6.7V23h6.6V24.4z M13.3,19.6H6.7v-1.4h6.6V19.6z'
            }
          ],
          animation: false,
          grid: {
            left: '5%',
            bottom: '10%',
            right: '7%',
            top: '13%'
          },
          xAxis: [
            {
              type: 'category',
              axisLine: {
                lineStyle: {
                  color: '#E1E4EB'
                }
              },
              axisLabel: {
                color: '#474C5C'
              },
              data: date
            }
          ],
          yAxis: {
            type: 'time',
            axisLine: {
              show: false
            },
            axisTick: {
              show: false
            },
            splitLine: {
              lineStyle: {
                color: '#EBECF0',
                type: 'dashed'
              }
            },
            axisLabel: {
              formatter: function(value) {
                return moment(value)
                  .format('YYYY-MM-DD HH:mm')
                  .substr(11, 20);
              }
            },
            min: (value) => {
              return value.min - 60 * 60 * 1000;
            },
            max: (value) => {
              const timeFormatter = moment(value.max).format('HH:mm');
              if (timeFormatter < targetTime) {
                const endTime = moment(targetTime).format('HH');
                const finalTime =
                  new Date(new Date().toLocaleDateString()).getTime() +
                  Number(endTime) * 60 * 60 * 1000;
                return finalTime;
              } else {
                return value.max;
              }
            }
          },
          series: [
            {
              data: finalCompletionTime,
              type: 'bar',
              barMaxWidth: 16,
              name: '最后完成时间',
              markLine: {
                data: [
                  {
                    yAxis: targetTime,
                    lineStyle: { color: '#F5A623 ' },
                    label: {
                      formatter(params) {
                        return params.value.slice(11);
                      }
                    }
                  }
                ]
              }
            },
            {
              type: 'line',
              name: '目标时间',
              lineStyle: {
                type: 'dashed',
                width: 1,
                color: '#F5A623'
              }
            }
          ],
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              type: 'shadow'
            },
            formatter: (params) => {
              // console.log(params);
              // const value = params[0].axisValue + ' ' + params[0].value.split(' ')[1];
              return [
                params[0].axisValue,
                params[0].marker +
                  ' ' +
                  params[0].seriesName +
                  '： ' +
                  params[0].data.value,
                params[0].marker +
                  ' ' +
                  '完成员工' +
                  '：  ' +
                  params[0].data.person
              ].join('<br>');
            }
          },
          color: ['#2F6AFF']
        };
      } else {
        this.$message.error(`近期工作完结查询失败:${error.message}`);
      }
    });
  }
  // 本周岗位工作量
  getThisweekWorkload() {
    thisweekWorkloadList().then(({ data, error }) => {
      if (data) {
        this.loading = false;
        this.thisweekWorkloadData = data;
        data.forEach((item) => {
          const name: any = [];
          const normalHandle: any = [];
          const timeOutHandle: any = [];
          data.forEach((item) => {
            name.push(item.name);
            normalHandle.push(item.normalHandle);
            timeOutHandle.push(item.timeOutHandle);
          });
          this.barOptions = {
            dataZoom: [
              {
                type: 'slider',
                height: 10,
                maxValueSpan: 4,
                bottom: 8,
                right: '7%',
                left: '7%',
                showDetail: false,
                handleIcon:
                  'M10.7,11.9H9.3c-4.9,0.3-8.8,4.4-8.8,9.4c0,5,3.9,9.1,8.8,9.4h1.3c4.9-0.3,8.8-4.4,8.8-9.4C19.5,16.3,15.6,12.2,10.7,11.9z M13.3,24.4H6.7V23h6.6V24.4z M13.3,19.6H6.7v-1.4h6.6V19.6z'
              }
            ],
            xAxis: [
              {
                type: 'category',
                axisLine: {
                  lineStyle: {
                    color: '#E1E4EB'
                  }
                },
                axisLabel: {
                  color: '#95A4BC'
                },
                data: name
              }
            ],
            yAxis: [
              {
                type: 'value',
                axisLine: {
                  show: false
                },
                axisTick: {
                  show: false
                },
                splitLine: {
                  lineStyle: {
                    color: '#EBECF0',
                    type: 'dashed'
                  }
                }
              }
            ],
            legend: {
              icon: 'circle',
              itemWidth: 10,
              right: '7%',
              top: '7%',
              data: ['超时处理', '正常处理']
            },
            grid: {
              left: '3%',
              bottom: '9%',
              containLabel: true
            },
            series: [
              {
                name: '正常处理',
                type: 'bar',
                stack: 'detalStatus',
                barWidth: 16,
                data: normalHandle,
                barMinHeight: 10
              },
              {
                name: '超时处理',
                type: 'bar',
                stack: 'detalStatus',
                barWidth: 16,
                data: timeOutHandle,
                barMinHeight: 10
              }
            ],
            color: ['#2F6AFF', '#F5A623']
          };
        });
      } else {
        this.$message.error(`本周岗位人员工作量查询失败:${error.message}`);
      }
    });
  }
  // 重点业务完成进度
  getProgressCompletion() {
    const param = {
      processName: this.keyTaskSelect
    };
    progressCompletionList(param).then(({ data, error }) => {
      if (data) {
        this.loading = false;
        this.progressCompletionData = data;
        const postName: any = [];
        const newOfday: any = [];
        const unresolved: any = [];
        data.forEach((item) => {
          postName.push(item.name);
          newOfday.push(item.newOfday);
          unresolved.push(item.unresolved);
        });
        this.progressOptions = {
          grid: {
            left: '2%',
            right: '2%',
            bottom: '5%',
            containLabel: true
          },
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              type: 'shadow'
            }
          },
          legend: {
            icon: 'circle',
            itemWidth: 10,
            right: '2%',
            top: '5%',
            textStyle: {
              // 图列内容样式
              color: '#95A4BC' // 字体颜色
            }
          },

          xAxis: {
            type: 'value',
            boundaryGap: [0, 0.01],
            axisLine: {
              // 坐标轴 轴线
              show: false,
              lineStyle: {
                color: '#95A4BC'
              }
            },
            splitLine: {
              lineStyle: {
                color: '#EBECF0',
                type: 'dashed'
              }
            },
            axisTick: {
              // 坐标轴 刻度
              show: false // 是否显示
            }
          },
          yAxis: {
            type: 'category',
            data: postName,
            axisLine: {
              // 坐标轴 轴线
              show: true, // 是否显示
              lineStyle: {
                color: '#95A4BC',
                type: 'solid',
                width: 0.5
              }
            },
            axisTick: {
              // 坐标轴的刻度
              show: false // 是否显示
            }
          },
          series: [
            {
              name: '当日新增',
              type: 'bar',
              barMinHeight: 10,
              barWidth: 13,
              data: newOfday
            },
            {
              name: '未结业务',
              barWidth: 13,
              barMinHeight: 10,
              type: 'bar',
              data: unresolved
            }
          ],
          color: ['#2F6AFF', '#F5A623']
        };
      } else {
        this.$message.error(`重点业务完成进度查询失败:${error.message}`);
      }
    });
  }
  getMemberDetailPend() {
    memberDetailPendList().then(({ data, error }) => {
      if (data) {
        this.loading = false;
        this.memberDetailPendData = data;
        const name: any = [];
        const otherPend: any = [];
        const pending: any = [];
        const quicklyPend: any = [];
        const timeoutPend: any = [];
        data.forEach((item) => {
          name.push(item.name);
          otherPend.push(item.otherPend); //当日新增
          pending.push(item.pending); //未结业务
          quicklyPend.push(item.quicklyPend); //未结业务
          timeoutPend.push(item.timeoutPend); //未结业务
        });
        this.dayOptions = {
          dataZoom: [
            {
              type: 'slider',
              height: 10,
              maxValueSpan: 4,
              bottom: 8,
              right: '7%',
              left: '7%',
              showDetail: false,
              handleIcon:
                'M10.7,11.9H9.3c-4.9,0.3-8.8,4.4-8.8,9.4c0,5,3.9,9.1,8.8,9.4h1.3c4.9-0.3,8.8-4.4,8.8-9.4C19.5,16.3,15.6,12.2,10.7,11.9z M13.3,24.4H6.7V23h6.6V24.4z M13.3,19.6H6.7v-1.4h6.6V19.6z'
            }
          ],
          grid: {
            left: '5%',
            bottom: '10%',
            right: '2%'
          },
          tooltip: {},
          xAxis: [
            {
              type: 'category',
              data: name,
              axisLine: {
                lineStyle: {
                  color: '#E1E4EB'
                }
              },
              axisLabel: {
                color: '#95A4BC'
              }
            }
          ],
          yAxis: [
            {
              type: 'value',
              axisLine: {
                show: false
              },
              axisTick: {
                show: false
              },
              splitLine: {
                lineStyle: {
                  color: '#EBECF0',
                  type: 'dashed'
                }
              }
            }
          ],
          legend: {
            icon: 'circle',
            itemWidth: 10,
            right: '2%',
            top: '5%',
            textStyle: {
              // 图列内容样式
              color: '#95A4BC' // 字体颜色
            }
          },
          series: [
            {
              name: '待办',
              type: 'bar',
              barWidth: 16,
              data: pending,
              barMinHeight: 10
            },
            {
              name: '已超时待办',
              type: 'bar',
              barWidth: 16,
              data: timeoutPend,
              barMinHeight: 10
            },
            {
              type: 'bar',
              name: '待办-尽快',
              barWidth: 16,
              data: quicklyPend,
              barMinHeight: 10
            },
            {
              type: 'bar',
              barWidth: 16,
              name: '待办-其他',
              data: otherPend,
              barMinHeight: 10
            }
          ],
          color: ['#2F6AFF', '#DF5146', '#F5A623', '#95A4BC']
        };
      } else {
        this.$message.error(`当日岗位人员待办任务查询失败:${error.message}`);
      }
    });
  }
  getCompletionDay() {
    completionDayList().then(({ data, error }) => {
      if (data) {
        this.loading = false;
        this.completionDayData = data;
      } else {
        this.$message.error(`当天工作完成进度查询失败:${error.message}`);
      }
    });
  }
  showVisible(val) {
    this.getProgressCompletion();
    if (val === true) {
      this.showVisibleRecently = true;
    } else {
      this.showVisibleRecently = false;
    }
  }
  handleChangeDayDuration() {
    this.getcompletionRecentWork();
  }
  changeNearWorkForm() {
    this.nearWorkForm = this.nearWorkForm === 'chart' ? 'table' : 'chart';
    this.getcompletionRecentWork();
  }
  changeEmploy() {
    this.empolyChange = this.empolyChange === 'chart' ? 'table' : 'chart';
  }
  init() {
    this.getList();
    this.getAnalysiWork(); //员工工作统计分析
    this.getcompletionRecentWork(); //近期工作完结
    this.getProgressCompletion(); //重点业务完成进度
    this.getMemberDetailPend();
    this.getCompletionDay(); //当天工作完成进度
    this.getThisweekWorkload(); //查询本周岗位工作量
  }
  //面板切换
  handleSearch() {
    if (this.currentTab === 2) {
      this.init();
    }
  }
  getProgressCompletionList() {
    getProgressCompletionList().then(({ data, error }) => {
      if (data && data.length > 0) {
        let arr = {};
        data.forEach((ele) => {
          arr = {
            key: ele,
            value: ele
          };
          this.keyTaskList.push(arr);
        });
      }
    });
  }
  handleChangeKeyTask(e) {
    if (!e) {
      this.getProgressCompletion();
    }
  }
  mounted() {
    this.init();
    this.getProgressCompletionList();
  }
}
