























































































































































































































































































import { Vue, Component } from 'vue-property-decorator';
import BaseChart from '@components/BaseChart/index.vue';
import BusinessLeaderPost from './businessLeaderPost.vue';
import moment from 'moment';
import { currencyFormatter } from '@utils/util';
import {
  queryDirectorTodayProgress,
  queryNearWorkProgress,
  queryCurrentWeekWorkload,
  queryEmployeeAnalysis,
  queryFundPostTable,
  queryProductLifeCycle,
  queryDirectorKeyTaskProgress,
  queryKeyTaskList
} from '@api/moreDimensionMonitor.js';

@Component({
  components: {
    BaseChart,
    BusinessLeaderPost
  }
})
export default class DirectorPage extends Vue {
  private currentTab: any = 1;
  private taskSelectDate: any = '';
  private keyTaskList: any = [];
  private keyTaskSelect: any = ['001', '003', '002'];
  private directorKeyTaskBar: any = {};
  private currentTime: any = '';
  private directorTodayTaskBar: any = {};
  private nearDayDuration: any = 7;
  private nearWorkForm: any = 'chart';
  private nearWorkBar: any = {};
  private nearWorkOnTimeNum: any = 0;
  private nearWorkTimeoutNum: any = 0;
  private nearWorkTableData: any = [];
  private nearWorkTableLoading = false;
  private nearWorkTableColumns: any = [
    { prop: 'date', label: '日期', align: 'center' },
    { prop: 'time', label: '最后完成时间', align: 'center' },
    { prop: 'lastFinishPerson', label: '完成员工', align: 'center' },
    { prop: 'targetFinishTime', label: '目标时间', align: 'center' }
  ];
  private currentWeekBar: any = {};
  private currentWeekTableLoading = false;
  private currentWeekTableData: any = [];
  private currentWeekTableColumns: any = [
    { prop: 'name', label: '岗位', minWidth: 180 },
    { prop: 'normalHandle', label: '正常处理', align: 'center' },
    { prop: 'timeOutHandle', label: '超时处理', align: 'center' }
  ];
  private employeeAnalysisForm: any = 'chart';
  private employeeAnalysisBar: any = {};
  private employeeAnalysisTableLoading = false;
  private employeeAnalysisTableData: any = [];
  private employeeAnalysisTableColumns: any = [
    { prop: 'name' },
    {
      prop: 'handledNum',
      label: '流程处理个数',
      sortable: true,
      align: 'center'
    },
    {
      prop: 'timeOutHandle',
      label: '超时处理',
      sortable: true,
      align: 'center'
    }
  ];
  private fundPostTableLoading = false;
  private fundPostTableData: any = [];
  private fundPostTableColumns: any = [
    { prop: 'name', label: '员工' },
    { prop: 'productNum', label: '产品数量', align: 'center' },
    { prop: 'generalOfferingNum', label: '公募基金', align: 'center' },
    // { prop: 'etfnum', label: 'ETF', align: 'center' },
    {
      prop: 'socialSecurityNum',
      label: '社保基本养老职业年金',
      align: 'center'
    },
    { prop: 'specialAccountNum', label: '专户', align: 'center' },
    { prop: 'companyAnnuityNum', label: '企业年金', align: 'center' }
  ];
  private productLifeCycleForm: any = 'chart';
  private productLifeCycleBar: any = {};
  private productCycleTableLoading = false;
  private productCycleTableData: any = [];
  private productCycleTableColumns: any = [
    { prop: 'name', label: '业务小组' },
    { prop: 'prepareNum', label: '准备', align: 'center' },
    { prop: 'raiseNum', label: '募集', align: 'center' },
    { prop: 'functioningNum', label: '运作', align: 'center' },
    { prop: 'terminatingNum', label: '终止中', align: 'center' },
    { prop: 'liquidationCompletedNum', label: '清算完毕', align: 'center' }
  ];

  private mounted() {
    this.currentTime = moment().format('HH:mm:ss');
    this.taskSelectDate = [
      moment()
        .subtract(7, 'days')
        .format('YYYY-MM-DD'),
      moment().format('YYYY-MM-DD')
    ];
    if (this.currentTab === 2) {
      this.init();
    }
  }
  init() {
    this.getKeyTaskList();
    this.getDirectorTodayProgress();
    this.getNearWorkProgress();
    this.getCurrentWeekWorkload();
    this.getEmployeeAnalysis();
    this.getFundPostTable();
    this.getProductLifeCycle();
    // this.getKeyTaskProgress();
  }
  //面板切换
  handleSearch() {
    if (this.currentTab === 2) {
      this.init();
    }
  }
  // 当日关键任务完成进度 时间选择
  handleKeyTaskTime() {
    this.getDirectorTodayProgress();
  }
  // 当日关键任务完成进度
  getDirectorTodayProgress() {
    queryDirectorTodayProgress({ time: this.currentTime }).then(
      ({ data, error }: { data: any; error: any }) => {
        if (!error) {
          const yAxis: any = [];
          const yAxis2: any = [];
          const completedList: any = [];
          const incompleteList: any = [];
          data.forEach((item: any) => {
            yAxis.push(item.productName);
            const tempAll = item.unfinishEvent + item.finishEvent;
            yAxis2.push(tempAll);
            completedList.push({
              value: item.percentage,
              count: item.finishEvent
            });
            incompleteList.push({
              value: 100 - item.percentage,
              count: item.unfinishEvent
            });
          });
          this.directorTodayTaskBar = {
            yAxis: [
              {
                type: 'category',
                axisLine: {
                  show: false
                },
                // axisTick: {
                //     show: false
                // },
                axisTick: {
                  alignWithLabel: true,
                  lineStyle: {
                    color: '#E1E4EB'
                  }
                },
                axisLabel: {
                  color: '#474C5C'
                },
                data: yAxis
              },
              {
                type: 'category',
                name: '总数',
                axisLine: {
                  show: false
                },
                axisTick: {
                  show: false
                },
                splitLine: {
                  show: false
                },
                nameTextStyle: {
                  padding: [0, -36, 0, 0]
                },
                nameGap: -15,
                data: yAxis2
              }
            ],
            xAxis: [
              {
                type: 'value',
                axisLine: {
                  lineStyle: {
                    color: '#E1E4EB'
                  }
                },
                splitLine: {
                  lineStyle: {
                    color: '#EBECF0',
                    type: 'dashed'
                  }
                },
                axisLabel: {
                  color: '#474C5C',
                  formatter: (value) => {
                    return value + '%';
                  }
                },
                data: yAxis2
              }
            ],
            series: [
              {
                name: '已完成',
                data: completedList,
                stack: 'progress',
                type: 'bar',
                label: {
                  show: true,
                  position: 'insideLeft',
                  formatter: (item) => {
                    return item.data.count;
                  }
                },
                barMaxWidth: 16
              },
              {
                name: '未完成',
                data: incompleteList,
                stack: 'progress',
                type: 'bar',
                label: {
                  show: true,
                  position: 'insideRight',
                  formatter: (item) => {
                    return item.data.count;
                  }
                },
                barMaxWidth: 16
              }
            ],
            tooltip: {
              formatter: (params) => {
                // console.log(params)
                const list = params.map((item) => {
                  return (
                    item.marker +
                    ' ' +
                    item.seriesName +
                    '： ' +
                    currencyFormatter(item.value) +
                    '%'
                  );
                });
                list.unshift(params[0].axisValue);
                return list.join('<br>');
              }
            },
            color: ['#70B82D', '#E1E4EB']
          };
        } else {
          this.$message.error('当日关键任务完成进度: ' + error.message);
        }
      }
    );
  }
  // 近期工作图表切换
  changeNearWorkForm() {
    this.nearWorkForm = this.nearWorkForm === 'chart' ? 'table' : 'chart';
  }
  // 近期工作完成 --- 周期切换
  handleChangeDayDuration() {
    this.getNearWorkProgress();
  }
  // 近期工作完结查询
  getNearWorkProgress() {
    this.nearWorkTableLoading = true;
    queryNearWorkProgress({ days: this.nearDayDuration }).then(
      ({ data, error }: { data: any; error: any }) => {
        if (!error) {
          this.nearWorkTableData = data[0].recentWorkSummaryEntityDoList;
          this.nearWorkOnTimeNum = data[0].onTimeNum;
          this.nearWorkTimeoutNum = data[0].timeoutNum;
          this.nearWorkTableLoading = false;
          const tempDate = data[0].recentWorkSummaryEntityDoList;
          if (tempDate.length) {
            const xAxis: any = [];
            const seriesData: any = [];
            const BASE_TIME = moment(new Date()).format('YYYY-MM-DD');
            const targetTime: any =
              BASE_TIME + ' ' + tempDate[0].targetFinishTime;
            // const targetTime: any = [];
            tempDate.forEach((item) => {
              xAxis.push(item.date);
              seriesData.push({
                value: BASE_TIME + ' ' + item.time,
                person: item.lastFinishPerson
              });
              // targetTime.push(tempDate[0].date + ' ' + item.targetFinishTime);
            });
            // console.log(seriesData);
            this.nearWorkBar = {
              xAxis: {
                data: xAxis
              },
              series: [
                {
                  type: 'bar',
                  name: '最后完成时间',
                  data: seriesData,
                  barMaxWidth: 20,
                  markLine: {
                    data: [
                      {
                        yAxis: targetTime,
                        lineStyle: { color: '#F5A623 ' },
                        label: {
                          formatter(params) {
                            return params.value.slice(11);
                          },
                          position: 'insideEndTop',
                          distance: [5, 10]
                        }
                      }
                    ]
                  }
                },
                {
                  type: 'line',
                  name: '目标时间'
                  // data: targetTime,
                  // lineStyle: {
                  //     type: 'dashed',
                  //     width: 1,
                  //     color: '#F5A623'
                  // }
                }
              ],
              yAxis: {
                type: 'time',
                axisLabel: {
                  formatter: function(value) {
                    return moment(value)
                      .format('YYYY-MM-DD HH:mm')
                      .substr(11, 20);
                  }
                },
                axisLine: {
                  show: false
                },
                axisTick: {
                  show: false
                },
                splitLine: {
                  lineStyle: {
                    color: '#EBECF0',
                    type: 'dashed'
                  }
                },
                min: (value) => {
                  return value.min - 60 * 60 * 1000;
                },
                max: (value) => {
                  if (new Date(targetTime).getTime() < value.max) {
                    return value.max;
                  } else {
                    // console.log(new Date(targetTime).getTime());
                    return new Date(targetTime).getTime();
                  }
                }
              },
              grid: {
                // right: '7%',
                bottom: '6%'
              },
              tooltip: {
                formatter: (params) => {
                  // const value = params[0].axisValue + " " + params[0].value.split(' ')[1];
                  return [
                    params[0].axisValue,
                    params[0].marker +
                      ' ' +
                      params[0].seriesName +
                      '： ' +
                      params[0].data.value,
                    params[0].marker +
                      ' ' +
                      '完成员工：' +
                      params[0].data.person
                  ].join('<br>');
                }
              },
              legend: {
                top: '0'
                // data: [{
                //     name: '目标时间',
                //     icon: ''
                // }, {
                //     name:  '最后完成时间',
                //     icon: 'circle'
                // }]
              },
              dataZoom: [
                {
                  type: 'slider',
                  height: 10,
                  maxValueSpan: 10,
                  bottom: 0,
                  right: '2%',
                  left: '7%',
                  showDetail: false,
                  handleIcon:
                    'M10.7,11.9H9.3c-4.9,0.3-8.8,4.4-8.8,9.4c0,5,3.9,9.1,8.8,9.4h1.3c4.9-0.3,8.8-4.4,8.8-9.4C19.5,16.3,15.6,12.2,10.7,11.9z M13.3,24.4H6.7V23h6.6V24.4z M13.3,19.6H6.7v-1.4h6.6V19.6z'
                }
              ],
              animation: false
            };
          }
        } else {
          this.$message.error('近期工作完结查询失败：' + error.message);
        }
      }
    );
  }
  // 本周岗位工作量查询
  getCurrentWeekWorkload() {
    this.currentWeekTableLoading = true;
    queryCurrentWeekWorkload().then(
      ({ data, error }: { data: any; error: any }) => {
        if (!error) {
          this.currentWeekTableLoading = false;
          this.currentWeekTableData = data;
          const xAxis: any = [];
          const normalList: any = [];
          const overtimeList: any = [];
          data.forEach((item: any) => {
            xAxis.push(item.name);
            normalList.push(item.normalHandle);
            overtimeList.push(item.timeOutHandle);
          });
          this.currentWeekBar = {
            xAxis: {
              data: xAxis
            },
            grid: {
              bottom: '6%'
            },
            series: [
              {
                type: 'bar',
                name: '正常处理',
                data: normalList,
                stack: 'currentWeek',
                barMaxWidth: 20
              },
              {
                type: 'bar',
                name: '超时处理',
                data: overtimeList,
                stack: 'currentWeek',
                barMaxWidth: 20
              }
            ],
            dataZoom: [
              {
                type: 'slider',
                height: 10,
                maxValueSpan: 4,
                bottom: 0,
                showDetail: false,
                handleIcon:
                  'M10.7,11.9H9.3c-4.9,0.3-8.8,4.4-8.8,9.4c0,5,3.9,9.1,8.8,9.4h1.3c4.9-0.3,8.8-4.4,8.8-9.4C19.5,16.3,15.6,12.2,10.7,11.9z M13.3,24.4H6.7V23h6.6V24.4z M13.3,19.6H6.7v-1.4h6.6V19.6z'
              }
            ],
            animation: false
          };
        } else {
          this.$message.error('本周岗位工作量查询失败：' + error.message);
        }
      }
    );
  }
  // 员工工作统计分析图表切换
  changeEmployeeAnalysisForm() {
    this.employeeAnalysisForm =
      this.employeeAnalysisForm === 'chart' ? 'table' : 'chart';
  }
  // 员工工作统计分析 查询
  getEmployeeAnalysis() {
    this.employeeAnalysisTableLoading = true;
    queryEmployeeAnalysis().then(
      ({ data, error }: { data: any; error: any }) => {
        if (!error) {
          this.employeeAnalysisTableData = data;
          this.employeeAnalysisTableLoading = false;
          const xAxis: any = [];
          const handleCountList: any = [];
          const overtimeHandleList: any = [];
          data.forEach((item: any) => {
            xAxis.push(item.name);
            handleCountList.push(item.handledNum);
            overtimeHandleList.push(item.timeOutHandle);
          });
          this.employeeAnalysisBar = {
            xAxis: {
              data: xAxis
            },
            series: [
              {
                type: 'bar',
                name: '流程处理个数',
                data: handleCountList,
                barMaxWidth: 10,
                barMinHeight: 10
              },
              {
                type: 'bar',
                name: '超时处理',
                data: overtimeHandleList,
                barMaxWidth: 10,
                barMinHeight: 10
              }
            ],
            grid: {
              bottom: '6%'
            },
            dataZoom: [
              {
                type: 'slider',
                height: 10,
                maxValueSpan: 8,
                bottom: 0,
                showDetail: false,
                handleIcon:
                  'M10.7,11.9H9.3c-4.9,0.3-8.8,4.4-8.8,9.4c0,5,3.9,9.1,8.8,9.4h1.3c4.9-0.3,8.8-4.4,8.8-9.4C19.5,16.3,15.6,12.2,10.7,11.9z M13.3,24.4H6.7V23h6.6V24.4z M13.3,19.6H6.7v-1.4h6.6V19.6z'
              }
            ],
            animation: false
          };
        } else {
          this.$message.error('员工工作统计分析数据查询失败：' + error.message);
        }
      }
    );
  }
  // 基金会计岗产品统计表 查询
  getFundPostTable() {
    this.fundPostTableLoading = true;
    queryFundPostTable().then(({ data, error }: { data: any; error: any }) => {
      if (!error) {
        this.fundPostTableData = data;
        this.fundPostTableLoading = false;
      } else {
        this.$message.error(
          '基金会计岗产品统计表数据查询失败：' + error.message
        );
      }
    });
  }
  // 产品生命周期数据查询
  getProductLifeCycle() {
    this.productCycleTableLoading = true;
    queryProductLifeCycle().then(
      ({ data, error }: { data: any; error: any }) => {
        if (!error) {
          this.productCycleTableLoading = false;
          this.productCycleTableData = data;
          const yAxis: any = data.map((item: any) => {
            return item.name;
          });
          const readyList = data.map((item: any) => {
            return item.prepareNum;
          });
          const raiseList = data.map((item: any) => {
            return item.raiseNum;
          });
          const operateList = data.map((item: any) => {
            return item.functioningNum;
          });
          const stopList = data.map((item: any) => {
            return item.terminatingNum;
          });
          const completedList = data.map((item: any) => {
            return item.liquidationCompletedNum;
          });
          const label = {
            show: true
            // formatter: (item) => {
            //     return item.data.count
            // }
          };
          this.productLifeCycleBar = {
            yAxis: {
              type: 'category',
              axisLine: {
                lineStyle: {
                  color: '#E1E4EB'
                }
              },
              axisLabel: {
                color: '#474C5C'
              },
              axisTick: {
                alignWithLabel: true
              },
              data: yAxis
            },
            xAxis: [
              {
                type: 'value',
                axisLine: {
                  show: false
                },
                axisTick: {
                  show: false
                },
                axisLabel: {
                  color: '#474C5C'
                },
                splitLine: {
                  lineStyle: {
                    color: '#EBECF0',
                    type: 'dashed'
                  }
                }
              }
            ],
            grid: {
              right: '2%'
            },
            series: [
              {
                name: '准备',
                type: 'bar',
                data: readyList,
                stack: 'productCycle',
                barMinHeight: 20,
                label,
                barMaxWidth: 20
              },
              {
                name: '募集',
                type: 'bar',
                data: raiseList,
                stack: 'productCycle',
                barMinHeight: 20,
                label,
                barMaxWidth: 20
              },
              {
                name: '运作',
                type: 'bar',
                data: operateList,
                stack: 'productCycle',
                barMinHeight: 20,
                label,
                barMaxWidth: 20
              },
              {
                name: '终止中',
                type: 'bar',
                data: stopList,
                stack: 'productCycle',
                barMinHeight: 20,
                label,
                barMaxWidth: 20
              },
              {
                name: '清算完成',
                type: 'bar',
                data: completedList,
                stack: 'productCycle',
                barMinHeight: 20,
                label,
                barMaxWidth: 20
              }
            ],
            color: ['#223273', '#1E50CE', '#3773DB', '#469DEB', '#2EC3C6']
          };
        } else {
          this.$message.error('产品生命周期数据查询失败：' + error.message);
        }
      }
    );
  }
  // 产品生命周期图表切换
  changeProductCycleForm() {
    this.productLifeCycleForm =
      this.productLifeCycleForm === 'chart' ? 'table' : 'chart';
  }
  // 关键任务完成进度 -- 时间选择
  handleChangeTaskDate() {
    this.getKeyTaskProgress();
  }
  // 关键任务完成进度 -- 关键任务选择
  handleChangeKeyTask(e) {
    // if (e.length) {
    //     this.getKeyTaskProgress();
    // }
    if (!e) {
      this.getKeyTaskProgress();
    }
  }
  // 关键任务完成进度查询
  getKeyTaskProgress() {
    if (!this.keyTaskSelect.length) {
      return this.$message.error('请选择关键任务');
    }
    if (!this.taskSelectDate) {
      return this.$message.error('请选择查询日期');
    }
    const params = {
      beginDate: this.taskSelectDate[0],
      endDate: this.taskSelectDate[1],
      productList: this.keyTaskSelect
    };
    // console.log(params);
    queryDirectorKeyTaskProgress(params).then(
      ({ data, error }: { data: any; error: any }) => {
        if (!error) {
          if (data.length) {
            const xAxis = data.map((item: any) => {
              return item.date;
            });
            const seriesData: any = [];
            const BASE_TIME = moment(new Date()).format('YYYY-MM-DD');
            data.forEach((item: any, itemIndex: any) => {
              item.taskProgressEntities.forEach((node: any, index: any) => {
                const value = BASE_TIME + ' ' + node.lastFinishTime;
                if (seriesData[index]) {
                  seriesData[index].data[itemIndex] = value;
                } else {
                  const obj = {
                    name: node.productName,
                    showSymbol: false,
                    data: [value],
                    type: 'line'
                  };
                  this.$set(seriesData, index, obj);
                }
              });
            });
            this.directorKeyTaskBar = {
              xAxis: {
                axisLine: {
                  lineStyle: {
                    color: '#E1E4EB'
                  }
                },
                axisLabel: {
                  color: '#474C5C'
                },
                data: xAxis
              },
              yAxis: {
                type: 'time',
                axisLine: {
                  show: false
                },
                axisTick: {
                  show: false
                },
                splitLine: {
                  lineStyle: {
                    color: '#EBECF0',
                    type: 'dashed'
                  }
                },
                axisLabel: {
                  formatter: function(value) {
                    return moment(value)
                      .format('YYYY-MM-DD HH:mm')
                      .substr(11, 20);
                  }
                }
              },
              legend: {
                show: false
              },
              grid: {
                // left: '5%',
                // right: '5%',
                top: '6%',
                bottom: '8%',
                containLabel: true
              },
              tooltip: {
                formatter: (params) => {
                  // console.log(params);
                  const list = params.map((item) => {
                    // const value = item.axisValue + " " + item.value.split(' ')[1];
                    const value = item.value.split(' ')[1];
                    return item.marker + ' ' + item.seriesName + '： ' + value;
                  });
                  list.unshift(params[0].axisValue);
                  return list.join('<br>');
                }
              },
              series: seriesData,
              dataZoom: [
                {
                  type: 'slider',
                  height: 10,
                  // left: '10%',
                  // right: '12%',
                  bottom: 0,
                  maxValueSpan: 10,
                  showDetail: false,
                  handleIcon:
                    'M10.7,11.9H9.3c-4.9,0.3-8.8,4.4-8.8,9.4c0,5,3.9,9.1,8.8,9.4h1.3c4.9-0.3,8.8-4.4,8.8-9.4C19.5,16.3,15.6,12.2,10.7,11.9z M13.3,24.4H6.7V23h6.6V24.4z M13.3,19.6H6.7v-1.4h6.6V19.6z'
                }
              ],
              animation: false
            };
          }
        } else {
          this.$message.error('关键任务完成进度查询失败：' + error.message);
        }
      }
    );
  }
  // 关键任务列表获取
  getKeyTaskList() {
    queryKeyTaskList().then(({ data, error }: { data: any; error: any }) => {
      if (!error) {
        this.keyTaskList = data;
        if (data.length) {
          this.keyTaskSelect = [data[0].taskId];
          this.getKeyTaskProgress();
        }
      } else {
        this.$message.error('关键任务列表获取失败：' + error.message);
      }
    });
  }
}
