    {
      label: '操作',
      minWidth: 200,
      align: 'center',
      actions: [
        {
          code: 'detail',
          text: '查看详情',
          onClick: this.detail
        }
      ]
    }
  ];
  // 删除空字段
  deleteEmptyParam(params) {
    for (const key in params) {
      if (
        params[key] === '' ||
        params[key] === undefined ||
        params[key] === null
      ) {
        delete params[key];
      }
    }
  }
  getList() {
    this.loading = true;
    this.tableList = [];
    const params = {
      limit: this.limit,
      page: this.page,
      type: this.activeName
    };

    getprocesslist(params).then(({ data }) => {
      if (data) {
        this.loading = false;
        this.tabList = data;
        if (this.activeName == '0') {
          this.tableList = data[0].processDoList;
          this.total = data[0].total;
        } else if (this.activeName == '1') {
          this.tableList = data[1].processDoList;
          this.total = data[1].total;
        } else if (this.activeName == '2') {
          this.tableList = data[2].processDoList;
          this.total = data[2].total;
        } else if (this.activeName == '3') {
          this.tableList = data[3].processDoList;
          this.total = data[3].total;
        } else if (this.activeName == '4') {
          this.tableList = data[4].processDoList;
          this.total = data[4].total;
        } else if (this.activeName == '5') {
          this.tableList = data[5].processDoList;
          this.total = data[5].total;
        } else {
          this.tableList = data[6].processDoList;
          this.total = data[6].total;
        }
      }
    });
  }
  getMarket() {
    queryMarket().then(({ data }) => {
      if (data) {
        this.marketList = data;
        this.defaultChecked = [];
        this.marketList.push({ market: '', id: 'jiade' });
        this.marketList.forEach((ele) => {
          if (ele.checked === '1') {
            this.defaultChecked.push(ele.market);
          }
        });
      }
    });
  }
  // 月份切换
  monthChange(data) {
    this.changeDate = data;
  }
  // 年份切换
  changeYear(data) {
    this.changeDate = data;
  }
  detail(row) {
    // 跳转任务办理页面
    // this.cacheParamData();
    if (row.close) {
      this.processInstanceId = row.processInstanceId;
      // 弹出任务办理状态流程流程图
      this.histroyDialog = true;
    } else {
      this.$router.push({
        path: '/processCenter/taskDeal',
        query: {
          taskId: row.id,
          mode: 'deal'
        }
      });
    }
  }
  // 关闭流转历史弹出
  cancelHistroyDialog() {
    this.histroyDialog = false;
  }

  deleteRow() {}
  handleSizeChange(val) {
    this.page = 1;
    this.limit = val;
    this.getList();
  }
  handleCurrentChange(val) {
    this.page = val;
    this.getList();
  }
  activeTabChange(val) {
    this.page = 1;
    this.getList();
  }
  activeTabChangeXtgl(val) {
    this.activeName = '6';
    this.page = 1;
    this.getList();
  }
  hoverDay(val) {
    let dateValue: any = [];
    dateValue = val.date.split('/');
    if (dateValue[1] < 10) {
      dateValue[1] = '0' + dateValue[1];
    }
    if (dateValue[2] < 10) {
      dateValue[2] = '0' + dateValue[2];
    }
    dateValue = dateValue.join('-');
    this.addMemoDate = dateValue;
  }
  cellClick(row) {
    if (row.id) {
      this.contentTishi = '修改';
    } else {
      this.contentTishi = '新增';
    }
    row.isEdit = false;
    this.dataList.forEach((ele, index) => {
      if (ele.id === row.id) {
        this.dataList.splice(index, 1, row);
      }
    });
  }
  changeRecord(row) {
    row.isEdit = true;
    if (this.addMemoDate) {
      row.date = this.addMemoDate;
    } else {
      row.date = moment()
        .locale('zh-cn')
        .format('YYYY-MM-DD');
    }

    // delete row.isEdit;
    this.deleteEmptyParam(row);
    addRecord({ date: row.date, title: row.title }).then(({ error }) => {
      if (!error) {
        this.$message({
          message: `${this.contentTishi}备忘录成功!`,
          type: 'success'
        });
        const addMemo: any = this.$refs.workCalendar;
        addMemo.monthlyStateQueryDate();
      } else {
        this.$message({
          message: `${this.contentTishi}备忘录失败：${error.message}`,
          type: 'error'
        });
      }
    });
  }
  // 新建备忘录
  addRecords() {
    if (this.dataList[0] && this.dataList[0].address === '') {
      this.$message({
        message: '备忘录信息不能为空',
        type: 'warning'
      });
    } else {
      this.dataList.unshift({
        address: '',
        isEdit: false
      });
    }
  }
  // 删除备忘录
  delRow(row) {
    if (row.id) {
      delRecord({ id: row.id }).then(({ error }) => {
        if (!error) {
          this.$message({
            message: '删除成功',
            type: 'success'
          });
          const addMemo: any = this.$refs.workCalendar;
          addMemo.monthlyStateQueryDate();
        } else {
          this.$message({
            message: `删除失败：${error.message}`,
            type: 'error'
          });
        }
      });
    } else {
      this.dataList.shift();
    }
  }
  exportCalendar() {
    exportCalendar({ markets: this.defaultChecked }).then(() => {});
  }
  importCalendar() {
    this.dialogVisible = true;
    const uploadFile = new FormData();
    this.fileList.forEach((item) => uploadFile.append('uploadFile', item.raw));
    importCalendar(uploadFile).then(({ error }) => {
      if (!error) {
        this.dialogVisible = false;
        this.$message.success('导入文件成功！');
      } else {
        this.dialogVisible = false;
        this.$message.error(`导入文件失败${error.message}`);
      }
    });
  }
  handleSelectionChange(selection) {
    if (selection.length > 0 && selection.length === this.dataList.length) {
      this.isWancheng = '取消完成';
    } else if (
      selection.length > 0 &&
      selection.length !== this.dataList.length
    ) {
      this.isWancheng = '全部完成';
    } else {
      this.isWancheng = '全部完成';
    }
    const arr: any = [];
    if (selection.length && selection.length > 0) {
      selection.map((item) => {
        if (!arr.includes(item.id)) {
          arr.push(item.id);
        }
      });
    } else {
      if (!arr.includes(selection.id)) {
        arr.push(selection.id);
      }
    }

    finishRecord({ ids: arr, date: this.currentDate }).then(({ error }) => {
      const childMethod: any = this.$refs.workCalendar;
      // if (error) {
      //   this.$message.error(`${error.message}`);
      // }
      if (!error) {
        childMethod.monthlyStateQueryDate(arr);
      } else {
        this.$message.error(`${error.message}`);
      }
    });
  }
  selectChange(selection) {
    this.handleSelectionChange(selection);
  }
  changeNodes(val) {
    this.defaultChecked = val;
  }
  getRowKey(row) {
    return row.id;
  }
  toggleSelection(rows) {
    const multipleTable: any = this.$refs.multipleTable;
    if (rows) {
      rows.forEach((row) => {
        multipleTable.toggleRowSelection(row, true);
      });
    } else {
      multipleTable.clearSelection();
    }
  }
  fileChange(fileList) {
    this.fileList = fileList;
    if (this.fileList.length === 0) {
      this.$message.error('请选择导入文件!');
    } else {
      this.importCalendar();
    }
  }
  created() {
    if (this.postNature && this.postNature === 'xtgl') {
      this.activeNameXtgl = '0';
      this.activeName = '6';
    } else {
      this.activeName = '0';
    }
    this.getList();
    this.getMarket();
  }
  getVal(val, selectedVal, currentDate) {
    this.dataList = val;
    this.currentDate = currentDate;
    if (selectedVal && selectedVal.length > 0) {
      this.toggleSelection(selectedVal);
    }
  }
  callBackInfo(data) {
    this.infos = data;
  }
}
